import { Component, Inject, OnInit } from "@angular/core";
import { UntypedFormGroup } from "@angular/forms";
import { FormlyFieldConfig } from "@ngx-formly/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import moment from 'moment';
import { IMyStatementsSettingsService } from "../../@intermediate/my-statements-settings";
import
  {
    GetStatementPropertySettingsRequest,
    GetStatementPropertySettingsResponse,
    PostStatementPropertySettingsRequest,
    PostStatementPropertySettingsRequestForm,
  } from "../../@intermediate/statement-property-settings";
@Component({
  selector: "tei-my-account-pages-statement-property-settings",
  templateUrl: "./statement-property-settings.component.html",
  styleUrls: ["./statement-property-settings.component.scss"],
})
export class PropertyBalanceSettingsComponent implements OnInit
{
  model: GetStatementPropertySettingsResponse;
  form = new UntypedFormGroup({});
  breadCrumbData: BreadCrumb[];
  isLoading = false;
  fields: FormlyFieldConfig[] = [
    {
      wrappers: ["formly-cutom-setting-card-wrapper"],
      templateOptions: {},
      fieldGroup: [
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "Other Balance Displays",
            description: `Select which balance types you'd like to display to your members.`,
          },
          fieldGroup: [
            {
              key: "DisplayAccountBalance",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: " Account Balance",
              },
            },
            {
              key: "DisplayPropertyBalance",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: " Property Balance",
              },
            },
            {
              key: "DisplayInvestorBalance",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: " Investor Balance",
              },
            },
            {
              key: "DisplayUtilityBalance",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: " Utility Balance",
              },
            },
            {
              key: "DisplayTotalBalance",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: " Total Balance",
              },
            },
          ],
        },
        {
          key: "DisplayPropertyExpirDate",
          type: "formly-custom-setting-toggle",
          templateOptions: {
            label: "Expiration Date",
            description: `Display the member's property expiration date on my statements.`,
          },
        },
        {
          key: "PropertyDuesStartDate",
          type: "formly-custom-setting-date-picker",
          templateOptions: {
            label: "Property Dues Start Date",
            inputLabel: "Start Date",
            description: ` Display property dues from this date forward.`,
          },
        },
      ],
    },
  ];

  constructor(
    @Inject("IMyStatementsSettingsService")
    private myStatementsSettingsService: IMyStatementsSettingsService,
    private snackBar: SnackBarService
  ) { }

  ngOnInit()
  {
    this.form = new PostStatementPropertySettingsRequestForm();
    this.setBreadCrumbData();
    this.load();
  }

  setBreadCrumbData()
  {
    this.breadCrumbData = [
      {
        title: "Web Settings",
        relativeUrl: "/office/module-settings/overview/",
      },
      {
        title: "My Account",
        relativeUrl: "/office/module-settings/overview/f9ef40a5-d86c-4b1b-97d8-5fa273e5eb26",
      },
      {
        title: "My Statements",
        relativeUrl: "../my-statements-settings",
      },
      {
        title: "Property Balance Settings",
        relativeUrl: "",
      },
    ];
  }

  load()
  {
    this.isLoading = true;
    const request = new GetStatementPropertySettingsRequest();
    this.myStatementsSettingsService.GetPropertySettings(request).subscribe(
      (response) =>
      {
        this.model = response;
        if (this.model)
        {
          this.form.setValue(response);
          this.isLoading = false;
        }
      },
      (error) =>
      {
        this.snackBar.showInfo(error.error.title, "", null, "center", "bottom");
        this.isLoading = false;
      }
    );
  }

  save()
  {
    this.isLoading = true;
    let request = new PostStatementPropertySettingsRequest();
    request = this.form.value;
    request.PropertyDuesStartDate = moment(
      this.form.value.PropertyDuesStartDate
    ).format("YYYY-MM-DD");
    this.myStatementsSettingsService.PostPropertySettings(request).subscribe(
      (response) =>
      {
        if (response.IsSuccess)
        {
          this.snackBar.showInfo(
            "successfully saved.",
            "",
            null,
            "left",
            "bottom"
          );
          this.isLoading = false;
        }
      },
      (error) =>
      {
        this.snackBar.showInfo(error.error.title, "", null, "left", "bottom");
        this.isLoading = false;
      }
    );
  }
}
