import { Component, Inject, OnInit } from '@angular/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { DeleteRegistrationsRequest, GetRegistrationsRequest, GetRegistrationsResponse, IRegistrationsService, Registration } from '../../@intermediate/registrations';

@Component({
  selector: 'tei-my-account-pages-registrations',
  templateUrl: './registrations.component.html',
  styleUrls: ['./registrations.component.scss']
})
export class RegistrationsComponent implements OnInit
{
  model: GetRegistrationsResponse;

  constructor(
    @Inject('IRegistrationsService') private RegistrationsService: IRegistrationsService,
    private confirmdialogService: ConfirmDialogService,
    private snackbarService: SnackBarService
  )
  {

  }

  ngOnInit()
  {
    this.load();
  }

  private load(request?: GetRegistrationsRequest)
  {
    if (!request)
    {
      request = new GetRegistrationsRequest();
    }

    if (!this.model)
    {
      this.model = new GetRegistrationsResponse();
    }

    this.RegistrationsService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  modify(registration: Registration)
  {
    if (registration.RegistrantTypeID == 'MEMBER')
    {
      window.location.href = "https://" + window.location.host + registration.AccessManagementURL + "/member-registration/" + registration.RegistrantTypeID + "/" + registration.RegistrationUID;
    }
    else
    {
      window.location.href = "https://" + window.location.host + registration.AccessManagementURL + "/guest-registration/" + registration.RegistrantTypeID + "/" + registration.RegistrationUID;
    }
  }

  cancel(registration: Registration)
  {
    const dialogData: DialogData = {
      title: 'Cancel',
      message: `Are you sure you want to cancel ${ registration.Title } for ${ registration.SubTitle }?`,
      textOkayButton: 'Confirm',
      textCancelButton: 'No'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteRegistrationsRequest();
        request.RegistrationUID = registration.RegistrationUID;

        this.RegistrationsService.Delete(request).subscribe(response =>
        {
          this.load();
          this.snackbarService.showSuccess(`'${ registration.Title }' cancelled successfully.`);
        });
      }
    });
  }
}
