import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import
{
  DeleteRegistrationsRequest,
  DeleteRegistrationsResponse,
  GetRegistrationsRequest,
  GetRegistrationsResponse,
  IRegistrationsService
} from '../@intermediate/registrations';

@Injectable({
  providedIn: 'root'
})

export class RegistrationsService implements IRegistrationsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Registrations';
  }

  public Get(request: GetRegistrationsRequest): Observable<GetRegistrationsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetRegistrationsResponse>(apiUrl, { params: request as any });
  }

  public Delete(request: DeleteRegistrationsRequest): Observable<DeleteRegistrationsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<GetRegistrationsResponse>(apiUrl, { params: request as any });
  }
}
