import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySnackBarModule as MatSnackBarModule } from '@angular/material/legacy-snack-bar';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AccountSecurityService } from '../../@services/account-security.service';
import { AccountSecurityComponent } from './account-security.component';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { ChangePasswordModule } from '../../components/change-password/change-password.module';
import { ChangePinModule } from '../../components/change-pin/change-pin.module';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatListModule, MatSnackBarModule, MatInputModule, MatSelectModule, MatDatepickerModule, MatNativeDateModule, MatCheckboxModule, MatRadioModule, MatExpansionModule, MatDialogModule, MatButtonModule];
const CORE_MODULES = [TeiTextTranslatePipeModule,TeiCurrencyTranslateModule];
const MODULES = [ChangePasswordModule, ChangePinModule];
@NgModule({
  declarations: [
    AccountSecurityComponent
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES,
    ...MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES
  ],
  providers: [
    { provide: 'IAccountSecurityService', useClass: AccountSecurityService }
  ]
})
export class AccountSecurityModule { }
