import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAccountSecurityDataRequest, GetAccountSecurityDataResponse, PostChangePasswordDataRequest, PostChangePasswordDataResponse } from '../@intermediate/account-security';
import { IAccountSecurityService } from '../@intermediate/account-security/account-security.interface';
import { PostChangePINDataRequest, PostChangePINDataResponse } from '../@intermediate/account-security/models/post-change-pin.model';

@Injectable({
  providedIn: 'root'
})

export class AccountSecurityService implements IAccountSecurityService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/AccountSecurity';
  }

  public Get(request: GetAccountSecurityDataRequest): Observable<GetAccountSecurityDataResponse>
  {
    const apiUrl = this.serviceUrl + "/GetAccountSecurity";
    return this.http.get<GetAccountSecurityDataResponse>(apiUrl, request);
  }

  public PostChangePassword(request: PostChangePasswordDataRequest): Observable<PostChangePasswordDataResponse>
  {
    const apiUrl = this.serviceUrl + "/PostChangePassword";
    return this.http.post<PostChangePasswordDataResponse>(apiUrl, request);
  }

  public PostChangePIN(request: PostChangePINDataRequest): Observable<PostChangePINDataResponse>
  {
    const apiUrl = this.serviceUrl + "/PostChangePIN";
    return this.http.post<PostChangePINDataResponse>(apiUrl, request);
  }
}
