import { Subject } from 'rxjs';
import { ItineraryFilter } from '../@intermediate/itinerary-filter/models/get-itinerary-filter.model';
import { Injectable } from '@angular/core';

@Injectable({providedIn: 'root'})
export class ItineraryFilterService {
    data$: Subject<ItineraryFilter> = new Subject<ItineraryFilter>();

    setData(data: ItineraryFilter) {
        this.data$.next(data);
    }
}