import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';

import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ItineraryFilterDialogComponent } from './itinerary-filter-dialog.component';
import { LoadingModule } from '@tei/material';
import { ItineraryFilterModule } from '../itinerary-filter/itinerary-filter.module';

const TEI_MATERIAL_MODULES = [
  LoadingModule,
  TeiTextTranslatePipeModule,
  ItineraryFilterModule,
  MatIconModule,
  MatButtonModule,
  MatDividerModule];

const BASE_MODULES = [CommonModule];
const MODULES = [];

const COMPONENTS = [ItineraryFilterDialogComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES, ...TEI_MATERIAL_MODULES],
  exports: [...COMPONENTS],
  declarations: [...COMPONENTS, ...PIPES ]
})
export class ItineraryFilterDialogModule { }
