import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostPaymentMethodsRequest, PostPaymentMethodsResponse } from '../@intermediate/payment-methods/models/post-payment-methods.model';
import { GetPaymentMethodsRequest, GetPaymentMethodsResponse, GetVoucherDetailsRequest, GetVoucherDetailsResponse, IPaymentMethodsService } from '../@intermediate/paymentmethods';

@Injectable({
  providedIn: 'root'
})

export class PaymentMethodsService implements IPaymentMethodsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/PaymentMethods';
  }

  public Get(request: GetPaymentMethodsRequest): Observable<GetPaymentMethodsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPaymentMethodsResponse>(apiUrl, { params: request as any });
  }

  public GetVoucherDetails(request: GetVoucherDetailsRequest): Observable<GetVoucherDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetVoucherDetails';
    return this.http.get<GetVoucherDetailsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPaymentMethodsRequest): Observable<PostPaymentMethodsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPaymentMethodsResponse>(apiUrl, request);
  }
}
