import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { IAccountSecurityService } from '../../@intermediate/account-security/account-security.interface';
import { PostChangePINDataRequest } from '../../@intermediate/account-security/models/post-change-pin.model';

@Component({
  selector: 'tei-my-account-change-pin',
  templateUrl: './change-pin.component.html',
  styleUrls: ['./change-pin.component.css']
})
export class ChangePinComponent implements OnInit
{

  encyptedPIN: string;
  setStatus: boolean;

  form = new UntypedFormGroup({
    OldPIN: new UntypedFormControl(''),
    NewPIN: new UntypedFormControl('', [Validators.required, Validators.minLength(4), Validators.maxLength(8)]),
    ConfirmPIN: new UntypedFormControl('', [Validators.required, Validators.minLength(4), Validators.maxLength(8)])
  });

  constructor(
    @Inject('IAccountSecurityService') private AccountSecurityService: IAccountSecurityService,
    public dialogRef: MatDialogRef<ChangePinComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private _snackBar: MatSnackBar)
  {
    this.setStatus = data.SetStatus;
  }

  ngOnInit(): void
  {
    this.setOldPinValidator();
  }

  onChangePin(event: string, controlName: string) {
    this.form.get(controlName).patchValue(event);
  }
  onNoClick(): void
  {
    this.dialogRef.close();
  }

  onCancel(): void
  {
    this.dialogRef.close();
  }

  setOldPinValidator()
  {
    if(this.setStatus)
    {
      const oldPinFormControl = this.form.get('OldPIN');
      oldPinFormControl.setValidators(Validators.required);
      oldPinFormControl.updateValueAndValidity();
    }
  }


  openSnackBar(message: string, action: string)
  {
    this._snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }

  save(form: UntypedFormGroup)
  {
    let request = new PostChangePINDataRequest();
    request.OldPIN = this.form.get('OldPIN').value.trim();
    request.NewPIN = this.form.get('NewPIN').value.trim();
    request.ConfirmPIN = this.form.get('ConfirmPIN').value.trim();

    if (request.NewPIN != request.ConfirmPIN)
    {
      this.openSnackBar("PINs didnt match.", "");
      return;
    }

    if (request.NewPIN.length < 4 || request.NewPIN.length > 8)
    {
      this.openSnackBar("PIN must be 4 - 8 digits.", "");
      return;
    }

    this.AccountSecurityService.PostChangePIN(request).subscribe(
      response =>
      {
        this.openSnackBar(response.ResultMessage, "");

        if (response.Result)
        {
          this.dialogRef.close();
        }
      },
      error =>
      {
        this.openSnackBar(error.error, "");
      });
  }
}
