import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { GetAccountSecurityDataRequest, GetAccountSecurityDataResponse } from '../../@intermediate/account-security';
import { IAccountSecurityService } from '../../@intermediate/account-security/account-security.interface';
import { ChangePasswordComponent } from '../../components/change-password/change-password.component';
import { ChangePinComponent } from '../../components/change-pin/change-pin.component';
import { PinBarcodeComponent } from '../../components/pin-barcode/pin-barcode.component';

@Component({
  selector: 'tei-my-account-account-security',
  templateUrl: './account-security.component.html',
  styleUrls: ['./account-security.component.css']
})
export class AccountSecurityComponent implements OnInit
{

  panelOpenState = false;
  model: GetAccountSecurityDataResponse;

  constructor(@Inject('IAccountSecurityService') private AccountSecurityService: IAccountSecurityService, public dialog: MatDialog, private snackBar: MatSnackBar) { }

  ngOnInit(): void
  {
    this.load();
  }

  load()
  {
    this.AccountSecurityService.Get(new GetAccountSecurityDataRequest()).subscribe(response =>
    {
      this.model = response;
    },
      error =>
      {
        this.openSnackBar(error.error, "");
      });
  }

  openPINDialog()
  {
    const dialogRef = this.dialog.open(ChangePinComponent, {
      width: '450px',
      data: {
        EncyptedPIN: this.model.EncyptedPIN,
        SetStatus: this.model.SetStatus
      }
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.load();
    });
  }


  ChangePasswordDialog()
  {
    const dialogRef = this.dialog.open(ChangePasswordComponent, {
      width: '450px',
      data: {}
    });

    dialogRef.afterClosed().subscribe(result =>
    {
    });
  }

  openSnackBar(message: string, action: string)
  {
    this.snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }

  openPINBarcodeDialog()
  {
    const dialogRef = this.dialog.open(PinBarcodeComponent, {
      maxWidth: "fit-content",
      panelClass: "barcode-dialog",
      data: {
        EncyptedPIN: this.model.EncyptedPIN,
        Name: this.model.Name
      }
    });

    dialogRef.afterClosed().subscribe(result =>
    {
    });
  }
}
