import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";

export class PostStatementPaymentSettingRequest
{
    public StatementPaymentSettings: StatementPaymentSetting;
}

export class PostStatementPaymentSettingResponse
{
    public EnableStatementPayments: boolean;
    public ApplyPaymentsTo: string;
    public EnableApplyPaymentByDefault: boolean;
    public PermittedPaymentAmount: string;
    public EnableCreditCardPayments: boolean;
    public EnableECheckOrACHPayments: boolean;
    public EnablePreAuthorizedPayments: boolean;
    public PreAuthorizedCreditCardMessage: string;
    public PreAuthorizedECheckMessage: string;
    public DisplayZeroOrFullyPaidInvoices: boolean;

}

export class StatementPaymentSetting 
{
    public EnableStatementPayments: boolean | "";
    public ApplyPaymentsTo: string;
    public EnableApplyPaymentByDefault: boolean | "";
    public PermittedPaymentAmount: string;
    public EnableCreditCardPayments: boolean | "";
    public EnableECheckOrACHPayments: boolean | "";
    public EnablePreAuthorizedPayments: boolean | "";
    public PreAuthorizedCreditCardMessage: string;
    public PreAuthorizedECheckMessage: string;
    public DisplayZeroOrFullyPaidInvoices: boolean | "";

}

export class PostStatementPaymentSettingRequestForm extends UntypedFormGroup
{
    constructor()
    {
        super({
            EnableStatementPayments: new UntypedFormControl(''),
            ApplyPaymentsTo: new UntypedFormControl(''),
            EnableApplyPaymentByDefault: new UntypedFormControl(''),
            PermittedPaymentAmount: new UntypedFormControl(''),
            EnableCreditCardPayments: new UntypedFormControl(''),
            EnableECheckOrACHPayments: new UntypedFormControl(''),
            EnablePreAuthorizedPayments: new UntypedFormControl(''),
            PreAuthorizedCreditCardMessage: new UntypedFormControl(''),
            PreAuthorizedECheckMessage: new UntypedFormControl(''),
            DisplayZeroOrFullyPaidInvoices: new UntypedFormControl('')
        });
    }

    public value: PostStatementPaymentSettingRequest;
    public setValue(
        value: PostStatementPaymentSettingResponse,
        options?: {
            onlySelf?: boolean;
            emitEvent?: boolean;
        }): void
    {
        let request = new PostStatementPaymentSettingRequest();
        request.StatementPaymentSettings = new StatementPaymentSetting();
        request.StatementPaymentSettings.EnableStatementPayments = value.EnableStatementPayments;
        request.StatementPaymentSettings.ApplyPaymentsTo = value.ApplyPaymentsTo;
        request.StatementPaymentSettings.EnableApplyPaymentByDefault = value.EnableApplyPaymentByDefault;
        request.StatementPaymentSettings.DisplayZeroOrFullyPaidInvoices = value.DisplayZeroOrFullyPaidInvoices;
        request.StatementPaymentSettings.PermittedPaymentAmount = value.PermittedPaymentAmount;
        request.StatementPaymentSettings.EnableCreditCardPayments = value.EnableCreditCardPayments;
        request.StatementPaymentSettings.EnableECheckOrACHPayments = value.EnableECheckOrACHPayments;
        request.StatementPaymentSettings.EnablePreAuthorizedPayments = value.EnablePreAuthorizedPayments;
        request.StatementPaymentSettings.PreAuthorizedCreditCardMessage = value.PreAuthorizedCreditCardMessage;
        request.StatementPaymentSettings.PreAuthorizedECheckMessage = value.PreAuthorizedECheckMessage;

        super.setValue(request.StatementPaymentSettings, options);
    }
}
