import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetStatementPaymentResponse, StatementPaymentDetail } from './get-statement-payment.model';


export class PostStatementPaymentRequest
{
  public StatementPayments: Array<StatementPaymentDetail>;
}

export class PostStatementPaymentRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      StatementPayments: new UntypedFormControl(''),
    });
  }

  public value: PostStatementPaymentRequest;

  public setValue(value: GetStatementPaymentResponse)
  {
    const request = new PostStatementPaymentRequest();

    request.StatementPayments = value.StatementPayments;

    super.setValue(request);
  }
}

export class PostStatementPaymentResponse
{
  public SessionID: string;
  public URL: string;
  public Message: string = null;
}
