import { Component, Inject, OnInit } from '@angular/core';
import { GetStatementURLRequest } from '../../@intermediate/statement-url/models/get-statement-url.model';
import { AccountDetail, GetStatementsRequest, GetStatementsResponse, IStatementsService, PostStatementsRequestForm, StatementDetail } from '../../@intermediate/statements';
//TODO refactor Address class to @tei/material-generic-filter
// import { FieldConfig } from '@tei/material-generic-filter/lib/field.interface';
import { BreadCrumb } from '@tei/material';
import { DynamicFormService } from '@tei/material-generic-filter';
import { FieldConfig } from '../../@intermediate/itinerary/models/get-itinerary.model';

@Component({
  selector: 'tei-my-account-pages-statements',
  templateUrl: './statements.component.html',
  styleUrls: ['./statements.component.scss']
})
export class StatementsComponent implements OnInit
{
  isLoading: boolean = false;
  model: GetStatementsResponse;
  form: PostStatementsRequestForm;
  intervalOfStatementsSeen: number = 10;
  numberOfStatementsSeen: number = this.intervalOfStatementsSeen;
  filterValue;
  exceptionMessage = "";
  accountList: FieldConfig;
  accountValue: string;
  preAuthorizedUrl: string;
  accountListWithDetails: AccountDetail[];
  hideMakePaymentDisplay: boolean;

  breadCrumbData: BreadCrumb[] = [
    {
      title: 'Web Settings',
      relativeUrl: '/office/contacts/web-settings'
    },
    {
      title: 'Statements',
      relativeUrl: ''
    }
  ];


  constructor(@Inject('IStatementsService') private StatementsService: IStatementsService,
    private dynamicfilter: DynamicFormService)
  {

  }

  ngOnInit()
  {
    this.form = new PostStatementsRequestForm();
    const request = new GetStatementsRequest();

    this.load(request);
    this.dynamicfilter.currentFilter.subscribe(filterValue =>
    {
      this.numberOfStatementsSeen = this.intervalOfStatementsSeen;
      this.filterValue = filterValue;
    });
  }

  private load(request: GetStatementsRequest)
  {
    if (!this.model)
    {
      this.model = new GetStatementsResponse();
    }

    this.isLoading = true;

    this.StatementsService.Get(request).subscribe(response =>
    {
      if (response.FilterConditions)
      {
        this.filterValue = [];
        this.filterValue['pendingonly'] = response.FilterConditions.filter(x => x.name === 'pendingonly').length > 0 ? response.FilterConditions.filter(x => x.name === 'pendingonly')[0].value : "";
        //this.filterValue["account"] = response.FilterConditions.filter(x=>x.name==='account').length > 0 ? response.FilterConditions.filter(x=>x.name==='account')[0].value : "";
      }
      this.model = response;
      //  this.accountList = this.model.AccountList;
      //  this.accountValue = this.model.AccountList.value

      this.accountListWithDetails = this.model.AllAccountWithDetails;
      let result = this.model.AllAccountWithDetails.find(s => s.SelectedValue != null);
      this.accountValue = result?.SelectedValue;

      this.model.PreAuthorizedLabel = result?.DisplayLabel;

      this.preAuthorizedUrl = this.model.PreAuthorizedURL + '/' + this?.accountValue;

      this.intervalOfStatementsSeen = this.model.PageingRowsCount ?? 10;

      this.hideMakePaymentDisplay = this.model.AllAccountWithDetails.find(s => s.SelectedValue != null)?.HideMakePaymentDisplay;

      this.form.setValue(response);

      this.isLoading = false;
    },
      error =>
      {
        //console.log(error["error"].Message);
        this.exceptionMessage = "No statements were found due to an internal error.";

        this.isLoading = false;
      });
  }

  save(form: PostStatementsRequestForm)
  {
    this.StatementsService.Post(form.value).subscribe(response =>
    {

    });
  }

  get ActualStatements()
  {
    var statements: StatementDetail[] = this.model.Statements;
    if (this.filterValue)
    {
      statements = statements.filter(x => x.CustAccountID === this.accountValue);
      if (this.filterValue['pendingonly'] === true)
      {
        statements = statements.filter(x => x.dOutstandingAmount > 0);
      }
    }
    return statements;
  }
  get StatementView()
  {
    if (this.ActualStatements)
    {
      return this.ActualStatements.slice(0, this.numberOfStatementsSeen);
    }
    else
    {
      return this.ActualStatements;
    }

  }

  FilterStatements(filterItem: string)
  {
    this.accountValue = filterItem;
    this.model.PreAuthorizedLabel = this.model.AllAccountWithDetails.find(s => s.AccountID == filterItem).DisplayLabel;
    this.hideMakePaymentDisplay = this.model.AllAccountWithDetails.find(s => s.AccountID == filterItem)?.HideMakePaymentDisplay;
    if (!this.model.AllAccountWithDetails.find(s => s.AccountID == filterItem).DisplayLabel)
    {
      this.model.PreAuthorizedLabel = "Off";
    }


    this.preAuthorizedUrl = this.model.PreAuthorizedURL + '/' + this.accountValue;
  }

  OpenPDF(m: StatementDetail)
  {
    if (m.ShowStatement && m.URL === "")
    {
      var req: GetStatementURLRequest = new GetStatementURLRequest();
      req.CustAccountID = m.CustAccountID;
      req.InvoiceID = m.InvoiceID;
      req.IsCustom = m.IsCustom;
      req.JobLineUID = m.JobLineUID;
      req.JobUID = m.JobUID;
      req.ReportID = m.ReportID;

      this.isLoading = true;
      this.StatementsService.GetStatementURL(req).subscribe(response =>
      {
        m.URL = response.URL;
        this.isLoading = false;
      },
        error =>
        {
          this.isLoading = false;
        });
    }
  }

  ShowMoreStatements()
  {
    if (this.numberOfStatementsSeen + this.intervalOfStatementsSeen > this.ActualStatements.length)
    {
      this.numberOfStatementsSeen = this.ActualStatements.length;
    }
    else
    {
      this.numberOfStatementsSeen = this.numberOfStatementsSeen + this.intervalOfStatementsSeen;
    }
  }

  get StatementBalance()
  {
    return this.ActualStatements.reduce((sum, current) => sum + current.dOutstandingAmount, 0);
  }

}
