import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { PaymentEngineSharedModule } from '@tei/common-payment-engine-shared';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SnackBarModule } from '@tei/material';
import { PaymentMethodsService } from '../../@services/paymentmethods.service';
import { PaymentMethodsComponent } from './paymentmethods.component';
const BASE_MODULES = [];
const MATERIAL_MODULES = [
  CommonModule, MatExpansionModule, MatButtonModule,
  MatIconModule, MatCardModule,
  MatTabsModule, MatInputModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [PaymentEngineSharedModule, SnackBarModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
  declarations: [PaymentMethodsComponent],
  imports: [
    RouterModule.forChild([
      { path: '', component: PaymentMethodsComponent },
      { path: 'add', loadChildren: () => import("../paymentmethod/paymentmethod.module").then(m => m.PaymentMethodModule), data: { NavigatedFromDynamicRouter: true } },
      { path: 'edit', loadChildren: () => import("../paymentmethod/paymentmethod.module").then(m => m.PaymentMethodModule), data: { NavigatedFromDynamicRouter: true } },
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IPaymentMethodsService', useClass: PaymentMethodsService }],
})
export class PaymentMethodsModule { }
