import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { RouterModule } from '@angular/router';
import { MyAccountSharedModule } from '@tei/common-my-account-shared';
import { MyAccountWidgetsModule } from '@tei/common-my-account-widgets';
import { PaymentEngineSharedModule } from '@tei/common-payment-engine-shared';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { PaymentMethodService } from '../../@services/paymentmethod.service';
import { PaymentMethodComponent } from './paymentmethod.component';


const BASE_MODULES = [
  FormsModule,
  ReactiveFormsModule,
  CommonModule
];
const CORE_MODULES = [TeiTextTranslatePipeModule];

const MATERIAL_MODULES = [
  MatIconModule,
  MatFormFieldModule,
  MatInputModule,
  MatButtonModule,
  MatSelectModule,
  MatCardModule,
  MatDialogModule
];
const TEI_MATERIAL_MODULES = [
  PaymentEngineSharedModule,
  MyAccountWidgetsModule, MyAccountSharedModule];

@NgModule({
  declarations: [PaymentMethodComponent],
  imports: [
    RouterModule.forChild([
      { path: '', component: PaymentMethodComponent },
      { path: ':id', component: PaymentMethodComponent }
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IPaymentMethodService', useClass: PaymentMethodService }],
})
export class PaymentMethodModule { }
