import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { GetTransactionSettingsResponse } from "./get-transaction-setting.model";
export class PostTransactionSettingsRequest {
  public EnableFBMinimums: boolean;
  public Pagination: number;
  public EnableDiscountColumn: boolean;
  public DiscountLabel: string;
  public EnableGratuityTips: boolean;
  public GratuityTipslabel: string;
  public EnableRewards: boolean;
  public RewardsLabel: string;
  public EnableCashEQ: boolean;
  public CashEQLabel: string;
}
export class PostTransactionSettingsResponse {}
export class PostTransactionSettingsRequestForm extends UntypedFormGroup {
  constructor() {
    super({
      EnableFBMinimums: new UntypedFormControl(),
      Pagination: new UntypedFormControl(),
      EnableDiscountColumn: new UntypedFormControl(),
      DiscountLabel: new UntypedFormControl(),
      EnableGratuityTips: new UntypedFormControl(true),
      GratuityTipslabel: new UntypedFormControl(),
      EnableRewards: new UntypedFormControl(),
      RewardsLabel: new UntypedFormControl(),
      EnableCashEQ: new UntypedFormControl(),
      CashEQLabel: new UntypedFormControl(),
    });
  }

  public value: PostTransactionSettingsRequest;

  public setValue(
    value: GetTransactionSettingsResponse,
    options?: { onlySelf?: boolean; emitEvent?: boolean }
  ): void {
    let request = new PostTransactionSettingsRequest();
    request.EnableFBMinimums = value.EnableFBMinimums ;
    request.Pagination = value.Pagination;
    request.EnableDiscountColumn = value.EnableDiscountColumn ;
    request.DiscountLabel = value.DiscountLabel ;
    request.EnableGratuityTips = value.EnableGratuityTips;
    request.GratuityTipslabel = value.GratuityTipslabel ;
    request.EnableRewards = value.EnableRewards;
    request.RewardsLabel = value.RewardsLabel ;
    request.EnableCashEQ = value.EnableCashEQ;
    request.CashEQLabel = value.CashEQLabel;
    super.setValue(request, options);
  }
}
