import { Component, EventEmitter, Input, OnInit, Output, SkipSelf } from "@angular/core";
import { ErrorStateMatcher } from "@angular/material/core";
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from "@tei/core";
import { GetStatementPaymentSettingResponse, PostStatementPaymentSettingRequestForm } from "../../@intermediate/statement-payment-setting";

@Component({
  selector: "tei-statement-payment-setting",
  templateUrl: "./statement-payment-setting.component.html",
  styleUrls: ["./statement-payment-setting.component.scss"],
  providers: [
    ngFormGroupControlValueAccessorProviders(StatementPaymentSettingComponent),
  ],
})
export class StatementPaymentSettingComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetStatementPaymentSettingResponse;
  form: PostStatementPaymentSettingRequestForm;
  preAuthorizedCreditCardMessage = 'Please add a credit card.';
  preAuthorizedECheckMessage = 'Please add a bank account.';
  displayZeroOrFullyPaidInvoices: boolean;
  @Input() openInvoice: string;
  @Input() displayPaidInvoices: boolean;
  @Output() notify = new EventEmitter<any>();


  constructor(@SkipSelf() protected _errorStateMatcher: ErrorStateMatcher)
  {
    super(_errorStateMatcher);
    this.form = new PostStatementPaymentSettingRequestForm();
  }

  ngOnInit()
  {
    this.updateDisplayZeroOrFullyPaidInvoices(this.openInvoice);
  }

  enablePaymentsChanged(event: any)
  {
    if (event)
      this.form.patchValue({
        PreAuthorizedCreditCardMessage: this.preAuthorizedCreditCardMessage,
        PreAuthorizedECheckMessage: this.preAuthorizedECheckMessage,
      });
    else
    {
      this.form.patchValue({
        PreAuthorizedCreditCardMessage: '',
        PreAuthorizedECheckMessage: '',
      });
    }
  }

  onChangeApplyPaymentsTo(event: any)
  {
    const value = event.value;
    this.updateDisplayZeroOrFullyPaidInvoices(value);
  }

  private updateDisplayZeroOrFullyPaidInvoices(value: string)
  {
    if (value === "OPEINV")
    {
      this.displayZeroOrFullyPaidInvoices = true;
      this.form.patchValue({ DisplayZeroOrFullyPaidInvoices: this.displayPaidInvoices });
    }
    else
    {
      this.displayZeroOrFullyPaidInvoices = false;
      this.form.patchValue({ DisplayZeroOrFullyPaidInvoices: false });
    }
  }
}
