import { MyProfileFieldCategory } from "../../myprofile-field-category";

export class GetMyProfileFieldChooserRequest
{
}

export class GetMyProfileFieldChooserResponse
{
  public Fields: Field[];
  public Categories: string[];
  public TEICategories: MyProfileFieldCategory[];
  public FieldOptions: FieldOption[];
}

export class Field
{
  public UDFUID: string;

  public UDFName: string;

  public CategoryName: string;

  public CategorySort: number;

  public FieldSort: number;

  public IsSelected: boolean;

  public Display: boolean = false;

  public FieldGroupID: number;

  public IsSystem: boolean;

  public UDFType: number;

  public UDFFieldFormat: string;

  public OrderID: number;

  public MasterFieldFormatID: number;
}

export class FieldOption
{
  public UDFUID: string;
  public Name: string;
}

