import { AfterContentChecked, Component, ElementRef, Inject, OnInit, QueryList, ViewChildren } from '@angular/core';
import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { GetMyProfileFieldInfoRequest, GetMyProfileFieldInfoResponse, IMyProfileFieldInfoService, PostMyProfileFieldInfoRequest } from '../../@intermediate/my-profile-field-info';
import { Address } from '../../@intermediate/my-profile-field-info/models/address.model';

@Component({
  selector: 'tei-my-account-pages-my-profile-field-info',
  templateUrl: './my-profile-field-info.component.html',
  styleUrls: ['./my-profile-field-info.component.scss']
})
export class MyProfileFieldInfoComponent implements OnInit, AfterContentChecked
{
  model: GetMyProfileFieldInfoResponse;
  addresses: Address[] = [];
  showSaveButton = false;

  fieldForm = new UntypedFormGroup({
    MyProfileFieldInfos: new UntypedFormArray([], [Validators.required])
  });

  myProfileFieldInfoArray = this.fieldForm.get('MyProfileFieldInfos') as UntypedFormArray;

  @ViewChildren('sharedFields') fields: QueryList<ElementRef>;
  request = new GetMyProfileFieldInfoRequest();
  isLoading: boolean = false;
  constructor(@Inject('IMyProfileFieldInfoService') private MyProfileFieldInfoService: IMyProfileFieldInfoService, private snackBar: MatSnackBar, private translate: TranslateService,
  )
  {

  }

  ngOnInit()
  {
    this.load(this.request);
  }

  private load(request: GetMyProfileFieldInfoRequest)
  {
    this.isLoading = true;
    if (!this.model)
    {
      this.model = new GetMyProfileFieldInfoResponse();
    }

    //This timer is to prevent the unknown issue of getting multiple service calls
    setTimeout(() =>
    {
      this.MyProfileFieldInfoService.Get(request).subscribe(response =>
      {
        this.model = response;
        this.fillFormArray();
        this.isLoading = false;
      },
        error => { error; });
    }, 100);

  }

  save(): void
  {
    const value: PostMyProfileFieldInfoRequest = { MyProfileFieldInfos: this.fields.map(field => field['getRawValue']()) };
    this.MyProfileFieldInfoService.Post(value).subscribe(
      response =>
      {
        this.openSnackBar(this.translate.instant('MY_ACCOUNT.MY_PROFILE_FILED_INFO.SAVED'), "");
        this.load(this.request);
      });
  }

  fillFormArray()
  {
    this.model.FieldGroups.forEach(group =>
    {
      this.myProfileFieldInfoArray.push(new UntypedFormControl());
    });
  }

  getMyProfileFieldInfoArray()
  {
    return this.fieldForm.get('MyProfileFieldInfos') as UntypedFormArray;
  }


  openSnackBar(message: string, action: string)
  {
    this.snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }

  get isSubFormsValid(): boolean
  {
    return (this.fields.map(m => m['isMasterFieldAnswersValid']) as boolean[]).every(Boolean);
  }

  ngAfterContentChecked(): void
  {
    this.showSaveButton = true;
  }
}
