import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAddressesRequest, GetAddressesResponse, IAddressesService, PostAddressesRequest, PostAddressesResponse } from '../@intermediate/addresses';

@Injectable({
  providedIn: 'root'
})

export class AddressesService implements IAddressesService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + 'Addresses';
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Addresses';
  }

  public Get(request: GetAddressesRequest): Observable<GetAddressesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAddressesResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostAddressesRequest): Observable<PostAddressesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostAddressesResponse>(apiUrl, request);
  }
}
