import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTransactionsRequest, GetTransactionsResponse, ITransactionsService } from '../@intermediate/transactions';

@Injectable({
  providedIn: 'root'
})

export class TransactionsService implements ITransactionsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + "Transactions";
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Transactions';
  }

  public Get(request: GetTransactionsRequest): Observable<GetTransactionsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetTransactionsResponse>(apiUrl, { params: <any>request });
  }
}
