import { Component, OnInit, Inject, Input, Output, EventEmitter } from '@angular/core';
import { ISelectAddressService, GetSelectAddressRequest, GetSelectAddressResponse } from '../../@intermediate/select-address';
import { Address } from '../../@intermediate/manage-address/models/address.model';

@Component({
  selector: 'tei-my-account-widgets-select-address',
  templateUrl: './select-address.component.html',
  styleUrls: ['./select-address.component.scss']
})
export class SelectAddressComponent implements OnInit
{
  @Input()  selectedAddressID: string;
  @Output() changedAddress = new EventEmitter<Address>();
  @Output() newAddressAddEvent = new EventEmitter<boolean>();

  model: GetSelectAddressResponse;
  isAddNewAddressButtonClicked: boolean;

  constructor(@Inject('ISelectAddressService') private SelectAddressService: ISelectAddressService)
  {
    this.isAddNewAddressButtonClicked = false;
  }

  ngOnInit()
  {
    const request = new GetSelectAddressRequest();
    this.load(request);
  }

  private load(request: GetSelectAddressRequest)
  {
    if (!this.model)
    {
      this.model = new GetSelectAddressResponse();
    }

    this.SelectAddressService.Get(request).subscribe(response =>
    {
      this.model = response;
    });

    this.changeAddress();
  }

  public changeAddress()
  {
    if (this.selectedAddressID)
    {
      this.changedAddress.emit(this.model.Addresses.filter(x => x.AddressID === this.selectedAddressID)[0]);
    }
  }

  addButtonClicked()
  {
    this.AddNewAddressButton = true;
  }

  addedNewAddress(event: any)
  {
    this.AddNewAddressButton = false;
    this.selectedAddressID = event.AddressID;
    this.model.Addresses.push(event);
    this.changedAddress.emit(this.model.Addresses.filter(x => x.AddressID === this.selectedAddressID)[0]);
  }

  set AddNewAddressButton(value: boolean)
  {
    this.isAddNewAddressButtonClicked = value;
    this.newAddressAddEvent.emit(value);
  }

  get filterAddressesByIsDisplayedFlag() {
    let temp: Array<Address>;
    if (this.model.Addresses)
    {
      temp  =  this.model.Addresses.filter( x => x.isDisplayed === 'True');
    }
    return temp;
  }
}
