import { MyProfileFieldCategory } from "../../myprofile-field-category";

export class GetMyProfileFieldRequest
{
}

export class GetMyProfileFieldResponse
{
  public MyProfileFields: MyProfileField[];

  public FieldGroups: MyProfileFieldCategory[];

  public TEIRoles: string[];
}


export class MyProfileField
{
  public FieldGroupName: string;
  public MasterFieldID: number;
  public IsSystem: boolean;
  public Title: string;
  public Roles: RoleSetting[];
  public Label: string;
  public FieldGroupMasterFieldID: number;
  public FieldGroupID: number;
  public SortOrder: number;
  public CategoryName: string;
  public FieldGroupOrder: number;
  public FieldGroupTypeName: string;
  public GroupFieldOrder: number;
}

export class RoleSetting
{
  public RoleIdentifier: string;
  public Value: RoleSettingEnum;
  public MasterFieldPermissionID: number;
  public Status: number;
}

export enum RoleSettingEnum
{
  AllowEdits = 1,
  ReadOnly = 2,
  Hide = 3,
  AllowEditsRequired = 4,
  AllowEditsPromptOnLogin = 5
}

export class FieldGroup
{
  public GroupName: string;
  public IsGroupBy: boolean;
}

export class TableList
{
  public Title: string;
  public RolesValue: Record<string, string> = {};
}

export class TableCategory
{
  public Title: string;
}
