import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import moment, { Moment } from "moment";
import { FieldConfig } from "../../@intermediate/itinerary";
import { ItineraryFilter, ItineraryFilterResult, TypeOfBookingEntity } from "../../@intermediate/itinerary-filter/models/get-itinerary-filter.model";

@Component({
  selector: 'tei-my-account-itinerary-filter',
  templateUrl: './itinerary-filter.component.html',
  styleUrls: ['./itinerary-filter.component.scss']
})
export class ItineraryFilterComponent
{
  @Input() filters: FieldConfig[] = [];
  @Input() isDialogMode: boolean = false;
  @Input() hideDateRange: boolean = false;
  @Input() submitOnInit: boolean = false;
  @Input() value: ItineraryFilter;
  @Output() onSubmit: EventEmitter<ItineraryFilterResult> = new EventEmitter();

  hasSubmitOnInit = false;

  typeOfBookingLabel: string;
  typeOfBookingEntitiesRaw: TypeOfBookingEntity[] = [];
  typeOfBookingEntities: TypeOfBookingEntity[] = [];
  neededTypeOfBookingIds: string[] = [];
  isAllSelected = true;
  isIndeterminate = false;

  dateRangeShow = false;
  dateRangeRawValue: FieldConfig;
  dateRangeLabel: string;
  dateRangeForm = new FormGroup({
    start: new FormControl<Date | Moment>(new Date()),
    end: new FormControl<Date | Moment>(new Date())
  });

  constructor() { }

  handleTypeOfBookingChange(filters: FieldConfig[]): void
  {
    const typeOfBooking = filters.filter(i => i.type === "chip").pop();
    if (typeOfBooking)
    {
      this.typeOfBookingLabel = typeOfBooking.label;
      this.typeOfBookingEntitiesRaw = typeOfBooking.options.map(m => ({
        name: m.display,
        value: m.value,
        isSelected: true,
      }));
    }
  }

  handleDateRangeChange(filters: FieldConfig[]): void
  {
    const dateRange = filters.filter(i => i.type === "date").pop();
    if (dateRange)
    {
      this.dateRangeRawValue = dateRange;
      this.dateRangeLabel = dateRange.label;
      if (dateRange.pastDateValue < 0)
      {
        const start = moment(dateRange.value1).subtract((dateRange.pastDateValue * -1), 'day').subtract(1, 'day').toDate();
        const end = dateRange.value2 ? new Date(dateRange.value2) : moment(dateRange.value1).subtract(1, 'day').toDate();
        this.dateRangeForm.setValue({
          start,
          end
        });
      } else
      {
        const start = moment(dateRange.value1).toDate();
        const end = dateRange.value2 ? new Date(dateRange.value2) : moment(dateRange.value1).add(dateRange.pastDateValue, 'day').toDate();
        this.dateRangeForm.setValue({
          start,
          end
        });
      }
      this.dateRangeShow = true;
    }
  }

  updateIndeterminate(): void
  {
    this.isIndeterminate = this.typeOfBookingEntities.some(i => i.isSelected);
  }
  updateAllSelected(): void
  {
    this.isAllSelected = this.typeOfBookingEntities.every(i => i.isSelected);
  }

  tabChanged(): void
  {
    this.typeOfBookingEntities = [];
  }

  setAll(state: boolean): void
  {
    this.typeOfBookingEntities.forEach(i => (i.isSelected = state));
    this.updateAllSelected();
    this.updateIndeterminate();
  }

  setCheckboxValue(state: boolean, index: number): void
  {
    this.typeOfBookingEntities[index].isSelected = state;
    this.updateAllSelected();
    this.updateIndeterminate();
  }

  getCheckboxValue(index: number): boolean
  {
    return this.typeOfBookingEntities[index].isSelected;
  }

  onSubmitClick(isInitialSubmit: boolean = false)
  {
    const result = new ItineraryFilter();
    result.startDate = moment.isMoment(this.dateRangeForm.value.start) ? this.dateRangeForm.value.start.toDate() : this.dateRangeForm.value.start;
    result.endDate = moment.isMoment(this.dateRangeForm.value.end) ? this.dateRangeForm.value.end.toDate() : this.dateRangeForm.value.end;
    result.typeOfBookings = this.typeOfBookingEntities.filter(item => item.isSelected).map(item => item.value);
    result.deselectedTypeOfBookings = this.typeOfBookingEntities.filter(item => !item.isSelected).map(item => item.value);
    this.onSubmit.emit({ isInitialSubmit, ...result });
  }

  renderFilters(types: (string | number)[]): void
  {
    this.handleTypeOfBookingChange(this.filters);
    this.typeOfBookingEntities = this.typeOfBookingEntitiesRaw.filter(f => types.includes(f.value));
    this.handleDateRangeChange(this.filters);
    this.afterFiltersRendered();
  }

  afterFiltersRendered(): void
  {
    if (this.submitOnInit && !this.hasSubmitOnInit)
    {
      this.hasSubmitOnInit = true;
      this.onSubmitClick(true);
    }
    if (this.value)
    {
      this.dateRangeForm.setValue({
        start: this.value.startDate,
        end: this.value.endDate
      });
      this.typeOfBookingEntities = this.typeOfBookingEntities.map(item => ({
        ...item,
        isSelected: (!this.value.deselectedTypeOfBookings.includes(item.value)) ? true : this.value.typeOfBookings.includes(item.value)
      }));
      if (!this.isDialogMode)
      {
        this.onSubmitClick();
      }
      this.updateAllSelected();
      this.updateIndeterminate();
    }
  }

}
