import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ManageAddressModule, MyAccountWidgetsModule } from '@tei/common-my-account-widgets';
import { AddressService } from '../../@services/address.service';
import { AddressComponent } from './address.component';

const BASE_MODULES = [];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [ManageAddressModule, MyAccountWidgetsModule];

@NgModule({
  declarations: [AddressComponent],
  imports: [
    RouterModule.forChild([
      { path: '', component: AddressComponent },
      { path: ':id', component: AddressComponent }
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IAddressService', useClass: AddressService }],
})
export class AddressModule { }
