import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { GetStatementPropertySettingsResponse } from "./get-statement-property-settings.model";

export class PostStatementPropertySettingsRequest {
  public DisplayAccountBalance: boolean;
  public DisplayInvestorBalance: boolean;
  public DisplayPropertyBalance: boolean;
  public DisplayPropertyExpirDate: boolean;
  public DisplayTotalBalance: boolean;
  public DisplayUtilityBalance: boolean;
  public MainBalanceDisplayLabel: string;
  public MainBalanceDisplayType: string;
  public PropertyBalanceDisplayType: string;
  public PropertyDuesStartDate: string;
}

export class StatementPropertySetting {
  public DisplayAccountBalance: boolean;
  public DisplayInvestorBalance: boolean;
  public DisplayPropertyBalance: boolean;
  public DisplayPropertyExpirDate: boolean;
  public DisplayTotalBalance: boolean;
  public DisplayUtilityBalance: boolean;
  public MainBalanceDisplayLabel: string;
  public MainBalanceDisplayType: string;
  public PropertyDuesStartDate: string;
  public PropertyBalanceDisplayType: string;
}

export class PostStatementPropertySettingsResponse
{

    public IsSuccess: boolean;

}


export class PostStatementPropertySettingsRequestForm extends UntypedFormGroup
{
    constructor()
    {
        super({
          MainBalanceDisplayLabel: new UntypedFormControl("Total Balance"),
          MainBalanceDisplayType: new UntypedFormControl(""),
          DisplayAccountBalance: new UntypedFormControl(""),
          DisplayPropertyBalance: new UntypedFormControl(""),
          DisplayInvestorBalance: new UntypedFormControl(""),
          DisplayUtilityBalance: new UntypedFormControl(""),
          DisplayTotalBalance: new UntypedFormControl(""),
          DisplayPropertyExpirDate: new UntypedFormControl(""),
          PropertyDuesStartDate: new UntypedFormControl(""),
          PropertyBalanceDisplayType: new UntypedFormControl(""),
        });
    }

    public value: PostStatementPropertySettingsRequest;
    public setValue(
        value: GetStatementPropertySettingsResponse,
        options?: {
            onlySelf?: boolean;
            emitEvent?: boolean;
        }): void
    {
        let request = new PostStatementPropertySettingsRequest();
        request.DisplayInvestorBalance=value.DisplayInvestorBalance;
        request.DisplayPropertyBalance=value.DisplayPropertyBalance;
        request.DisplayPropertyExpirDate=value.DisplayPropertyExpirDate;
        request.DisplayTotalBalance=value.DisplayTotalBalance;
        request.DisplayUtilityBalance=value.DisplayUtilityBalance;
        request.MainBalanceDisplayLabel=value.MainBalanceDisplayLabel;
        request.MainBalanceDisplayType=value.MainBalanceDisplayType;
        request.DisplayAccountBalance = value.DisplayAccountBalance;
        request.PropertyDuesStartDate=value.PropertyDuesStartDate;
        request.PropertyBalanceDisplayType = value.PropertyBalanceDisplayType;
        super.setValue(value, options);
    }
}
