import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAddressRequest, GetAddressResponse, IAddressService } from '../@intermediate/address';

@Injectable({
  providedIn: 'root'
})

export class AddressService implements IAddressService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + "Address";
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Address';
  }

  public Get(request: GetAddressRequest): Observable<GetAddressResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetAddressResponse>(apiUrl, { params: <any>request });
  }
}
