import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, FormTimePickerModule, LoadingModule, SettingsListModule } from '@tei/material';
import { MyAccountAdminRoutingModule } from '../../my-account-admin-routing.module';
import { ItinerarySettingModule } from '../itinerary-setting/itinerary-setting.module';
import { MyStatementsSettingsModule } from '../my-statements-settings/my-statements-settings.module';
import { MyprofileFieldModule } from '../myprofile-field/myprofile-field.module';
import { TransactionSettingsModule } from '../transaction-settings/transaction-settings.module';
import { ContactSettingsComponent } from './contact-settings.component';
import { MyProfileFieldCategoryService } from '../../@services/myprofile-field-category.service';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MATERIAL_MODULES = [MatIconModule];
const TEI_MATERIAL_MODULES = [FormTimePickerModule, MatDatepickerModule, MatNativeDateModule, LoadingModule, BreadCrumbModule, SettingsListModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const TEI_MODULES = [ItinerarySettingModule, MyStatementsSettingsModule, MyprofileFieldModule, TransactionSettingsModule];


@NgModule({
  declarations: [
    ContactSettingsComponent
  ],
  imports: [
    CommonModule,
    MyAccountAdminRoutingModule,
    ...BASE_MODULES,
    ...TEI_MODULES,
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ], providers: [{ provide: 'IMyProfileFieldCategoryService', useClass: MyProfileFieldCategoryService }]
})
export class ContactSettingsModule { }
