
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { MaterialGenericFilterModule } from '@tei/material-generic-filter';
import { MaterialPDFViewerModule } from '@tei/material-pdf-viewer';
import { StatementsService } from '../../@services/statements.service';
import { StatementsComponent } from './statements.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatExpansionModule, MatCardModule, MatButtonModule, MatIconModule, MatRippleModule, MatFormFieldModule, MatSelectModule];
const TEI_MATERIAL_MODULES = [MaterialPDFViewerModule, MaterialGenericFilterModule, LoadingModule, BreadCrumbModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
    declarations: [StatementsComponent],
    imports: [
        RouterModule.forChild([
            {
                path: '', component: StatementsComponent
            },
            {
                path: "payment",
                loadChildren: () => import('../statement-payment/statement-payment.module').then(m => m.StatementPaymentModule),
            }
        ]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...CORE_MODULES,
        ...TEI_MATERIAL_MODULES
    ],
    providers: [{ provide: 'IStatementsService', useClass: StatementsService }]
})
export class StatementsModule { }
