import { FieldConfig } from "../../itinerary/models/get-itinerary.model";

export class GetTransactionsRequest
{
  public StartDate: string;
  public EndDate: string;
  public ContactId: string = "";
  public IncludeCashEQ: string;
  public IncludeRewards: string;
  public RewardsLabel: string;
  public AllTypes: string;
  public AllLocations: string;
  public SelectedDates: string;
  public PageSize: number = 25;
  public PageCounter: number = 1;
}

export class GetTransactionsResponse
{
  public purchaseResponseHeaders: Array<PurchaseResponseHeaders>;
  public FilterConditions: Array<FieldConfig>;
  public Settings: GlobalSettings;
  public Total: number;
}



export class PurchaseResponseDetails
{
  public ReservationID: string;
  public StartDate: string;
  public StartDateText: string;
  public StartTime: string;
  public StartTimeText: string;
  public BookingText: string;
  public TransactionSource: string;
  public TransactionType: number;
  public CustomerID: string;
  public ReferenceID: string;
  public Location: string;
  public Description: string;
  public QTY: number;
  public PointsEarned?: number;
  public PaymentTypes: string;
  public TerminalID: string;
  public TrxID: string;
  public Price: string;
  public Discount: string;
  public Tax: string;
  public Total: string;
  public modifiers: Array<PurchaseResponseModifiers>;
}

export class PurchaseResponseModifiers
{
  public checkinternal: string;
  public modifierprice: string;
  public MainItemGroup: string;
  public modifierdescription: string;
}

export class PurchaseResponseHeaders
{
  public details: Array<PurchaseResponseDetails>;
  public ReservationID: string;
  public StartDate: string;
  public StartDateText: string;
  public StartTime: string;
  public StartTimeText: string;
  public BookingText: string;
  public TransactionSource: string;
  public TransactionType: number;
  public CustomerID: string;
  public ReferenceID: string;
  public Location: string;
  public Description: string;
  public QTY: number;
  public PointsEarned?: number;
  public PaymentTypes: string;
  public TerminalID: string;
  public TrxID: string;
  public Price: string;
  public Discount: string;
  public Tax: string;
  public Total: string;
  public TipTotal: string;
}

export class GlobalSettings
{
  public ShowDiscountColumn: boolean;
  public ShowTipTotal: boolean;
  public EnableRewards: boolean;
  public DiscountColumnLabel: string;
  public TipsLabel: string;
  public RewardsLabel: string;
}
