import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSelectAddressRequest, GetSelectAddressResponse, ISelectAddressService } from '../@intermediate/select-address';

@Injectable({
  providedIn: 'root'
})

export class SelectAddressService implements ISelectAddressService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + 'SelectAddress';
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/SelectAddress';
  }

  public Get(request: GetSelectAddressRequest): Observable<GetSelectAddressResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetSelectAddressResponse>(apiUrl, { params: request as any });
  }
}
