import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { IMyProfileFieldCategoryService, MyProfileFieldCategory, PostMyProfileFieldCategoryRequest } from '../../@intermediate/myprofile-field-category';
import { firstValueFrom } from 'rxjs';

@Component({
  selector: 'tei-my-account-update-myprofilefield-category',
  templateUrl: './update-myprofilefield-category.component.html',
  styleUrls: ['./update-myprofilefield-category.component.css']
})
export class UpdateMyprofilefieldCategoryComponent implements OnInit
{
  request: PostMyProfileFieldCategoryRequest;
  category: MyProfileFieldCategory;
  isLoading = false;

  constructor(public dialogRef: MatDialogRef<UpdateMyprofilefieldCategoryComponent>,
    @Inject('IMyProfileFieldCategoryService') private myProfileFieldService: IMyProfileFieldCategoryService,
    @Inject(MAT_DIALOG_DATA) public data: MyProfileFieldCategory)
  {
    this.category = data;
  }


  ngOnInit(): void
  {
    this.request = new PostMyProfileFieldCategoryRequest();
    this.request.Name = this.category.Name;
    this.request.FieldGroupID = this.category.FieldGroupID;
  }

  async onSubmit()
  {

    this.isLoading = true;

    try
    {
      await firstValueFrom(this.myProfileFieldService.Update(this.request));

      this.dialogRef.close(false);

    } catch (err)
    {
      console.log(err);
    } finally
    {
      this.isLoading = false;
    }

  }

  closeDialog(): void
  {
    this.dialogRef.close(false);
  }
}
