import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { ItinerarySettingComponent } from './itinerary-setting.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MatLegacyCheckboxModule } from '@angular/material/legacy-checkbox';
import { ItinerarySettingsService } from '../../@services/itinerary-settings.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [MatLegacyCheckboxModule, LoadingModule, BreadCrumbModule];

@NgModule({
  declarations: [ItinerarySettingComponent],
  imports: [
    ...BASE_MODULES,
    ...CORE_MODULES,
    ...MATERIAL_MODULES
  ],
  providers: [{ provide: 'IItinerarySettingsService', useClass: ItinerarySettingsService }]
})
export class ItinerarySettingModule { }
