import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';

@Component({
  selector: 'tei-itinerary-confirmation-dialog',
  templateUrl: './itinerary-confirmation-dialog.component.html',
  styleUrls: ['./itinerary-confirmation-dialog.component.scss']
})
export class ItineraryConfirmationDialogComponent implements OnInit
{
  title: string;
  message: string;
  confirmBtnText: string;

  constructor(public dialog: MatDialogRef<ItineraryConfirmationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ItineraryConfirmationDialogConfig)
  {

  }

  ngOnInit()
  {
    this.initialize();
  }

  initialize(): void
  {
    this.title = this.data.settings.title;
    this.message = this.data.settings.message;
    this.confirmBtnText = this.data.settings.confirmBtnText;
  }

  onNoClick()
  {
    this.dialog.close({
      isConfirmed: false,
      message: "click on close icon"
    } as ItineraryConfirmationDialogResult);
  }

  onConfirmClick()
  {
    this.dialog.close({
      isConfirmed: true,
      message: "click on Confirm button"
    } as ItineraryConfirmationDialogResult);
  }
}


export class ItineraryConfirmationDialogConfig
{
  settings: ItineraryConfirmationDialogSettings;
}

export class ItineraryConfirmationDialogSettings
{
  title: string;
  message: string;
  confirmBtnText: string;
}

export class ItineraryConfirmationDialogResult
{
  isConfirmed: boolean;
  message: string;
}
