import { Component, Inject, OnInit } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DynamicFormService } from '@tei/material-generic-filter';
import { take } from 'rxjs/operators';
import { GetTransactionsRequest, GetTransactionsResponse, ITransactionsService, PurchaseResponseHeaders } from '../../@intermediate/transactions';
import { Filter } from '../../@services/transactionfilter.service';
export class DialogData
{
  AllTypes: string;
}
import moment from 'moment';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-my-account-pages-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.scss']
})
export class TransactionsComponent implements OnInit
{

  filterTodayDate: moment.Moment = moment();
  filterCurrentMonth: moment.Moment = moment().startOf('month');
  model: GetTransactionsResponse;
  totalTransactions: number = 0;
  startDate: string;
  endDate: string;
  PageCounter: number = 1;
  purchaseResponseHeaders: PurchaseResponseHeaders[] = [];
  pageSize: number;
  intialPageSize: number;
  value: any;
  ChangeValue: any;
  todayDate: Date = new Date();
  currentMonth: Date = new Date(new Date().getFullYear(), new Date().getMonth(), 1);
  applyForm: any;
  filterValue: Filter;
  ShowDiscountColumn: boolean;
  ShowTipTotal: boolean;
  EnableRewards: boolean;
  DiscountColumnLabel: string;
  TipsLabel: string;
  RewardsLabel: string;
  formValues: any[] = [];

  breadCrumbData: BreadCrumb[] = [
    {
      title: 'Web Settings',
      relativeUrl: '/office/contacts/web-settings'
    },
    {
      title: 'Transactions',
      relativeUrl: ''
    }
  ];

  constructor(@Inject('ITransactionsService') private TransactionsService: ITransactionsService, public bottomSheet: MatBottomSheet, private dynamicfilter: DynamicFormService)
  {

  }

  ngOnInit()
  {
    const request = new GetTransactionsRequest();
    this.searchByFilters();
    this.dynamicfilter.currentFilter.subscribe(filterValue =>
    {
      if (filterValue)
      {
        this.formValues.push(filterValue);
        setTimeout(() =>
        {
          if (this.formValues.length == 2)
          {
            this.formValues = [];
            return;
          }
          else if (this.formValues.length == 1)
          {
            if (this.formValues[0].daterange.startdaterange != null && this.formValues[0].daterange.enddaterange != null)
            {
              this.filterTransactions(filterValue);
              this.formValues = [];
            }
            else
            {
              this.formValues = [];
            }
          }
        }, 300);
      }
    });
  }
  searchByFilters()
  {
    let request = new GetTransactionsRequest();
    request.AllTypes = '';
    request.AllLocations = '';
    request.StartDate = this.convertToLocaleDateString(this.currentMonth);
    request.EndDate = this.convertToLocaleDateString(this.todayDate);
    this.startDate = request.StartDate;
    this.endDate = request.EndDate;
    this.intialPageSize = request.PageSize;
    this.PageCounter = 1;
    this.load(request);
  }

  convertToLocaleDateString(value: Date)
  {
    return (value.getMonth() + 1).toString() + '/' + value.getDate().toString() + '/' + value.getFullYear().toString();
  }

  filterTransactions(value)
  {
    let request = new GetTransactionsRequest();
    this.ChangeValue = value;
    request.AllTypes = value.AllTypes;
    request.AllLocations = value.AllLocations;
    request.StartDate = this.convertToLocaleDateString(value.daterange.startdaterange.toDate());
    request.EndDate = this.convertToLocaleDateString(value.daterange.enddaterange.toDate());
    this.startDate = request.StartDate;
    this.endDate = request.EndDate;
    this.totalTransactions = 0;
    this.purchaseResponseHeaders = [];
    this.load(request);
  }
  loadMore()
  {
    let request = new GetTransactionsRequest();
    if (this.ChangeValue)
    {
      request.AllTypes = this.ChangeValue.AllTypes;
      request.AllLocations = this.ChangeValue.AllLocations;
      request.StartDate = this.convertToLocaleDateString(this.ChangeValue.daterange.startdaterange.toDate());
      request.EndDate = this.convertToLocaleDateString(this.ChangeValue.daterange.enddaterange.toDate());
      this.PageCounter += 1;
      request.PageCounter = this.PageCounter;
      this.load(request);
    }
    else
    {
      request.AllTypes = '';
      request.AllLocations = '';
      request.StartDate = this.convertToLocaleDateString(this.currentMonth);
      request.EndDate = this.convertToLocaleDateString(this.todayDate);
      this.PageCounter += 1;
      request.PageCounter = this.PageCounter;
      this.load(request);
    }
  }
  private load(request: GetTransactionsRequest)
  {
    this.model = new GetTransactionsResponse();

    this.TransactionsService.Get(request).pipe(take(1)).subscribe(response =>
    {
      if (this.ChangeValue)
      {
        // response.FilterConditions.filter(x => x.name == "daterange")[0].value = { startDate: this.ChangeValue.daterange.startDate, endDate: this.ChangeValue.daterange.endDate};
        response.FilterConditions.filter(x => x.name1 == "startdaterange")[0].value1 = this.ChangeValue.daterange.startdaterange;
        response.FilterConditions.filter(x => x.name2 == "enddaterange")[0].value2 = this.ChangeValue.daterange.enddaterange;
        response.FilterConditions.filter(x => x.name == "AllTypes")[0].value = this.ChangeValue.AllTypes;
        response.FilterConditions.filter(x => x.name == "AllLocations")[0].value = this.ChangeValue.AllLocations;
      }
      else
      {
        //response.FilterConditions.filter(x => x.name == "daterange")[0].value = { startDate: this.filterCurrentYear, endDate: this.filterTodayDate };
        response.FilterConditions.filter(x => x.name1 == "startdaterange")[0].value1 = this.filterCurrentMonth;
        response.FilterConditions.filter(x => x.name2 == "enddaterange")[0].value2 = this.filterTodayDate;
      }
      this.model = response;
      this.pageSize = response.purchaseResponseHeaders.length;
      this.ShowDiscountColumn = response.Settings.ShowDiscountColumn;
      this.DiscountColumnLabel = response.Settings.DiscountColumnLabel;
      this.ShowTipTotal = response.Settings.ShowTipTotal;
      this.TipsLabel = response.Settings.TipsLabel;
      this.EnableRewards = response.Settings.EnableRewards;
      this.RewardsLabel = response.Settings.RewardsLabel;

      response.purchaseResponseHeaders.forEach(element =>
      {
        const exists = this.purchaseResponseHeaders.some(
          x => x.TrxID === element.TrxID && x.ReferenceID === element.ReferenceID
        );

        if (!exists)
        {
          this.purchaseResponseHeaders.push(element);
        }
      });




      if (this.pageSize != 0)
      {
        this.totalTransactions = response.Total;
      }
    });

  }
}
