import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { GetVoucherDetailsResponse, VoucherDetailDialogComponent } from '@tei/common-payment-engine-shared';
import { SnackBarService } from '@tei/material';
import { PostPaymentMethodsRequest } from '../../@intermediate/payment-methods/models/post-payment-methods.model';
import { GetPaymentMethodsRequest, GetPaymentMethodsResponse, GetVoucherDetailsRequest, IPaymentMethodsService, VoucherDetail } from '../../@intermediate/paymentmethods';

@Component({
  selector: 'tei-my-account-pages-paymentmethods',
  templateUrl: './paymentmethods.component.html',
  styleUrls: ['./paymentmethods.component.scss']
})
export class PaymentMethodsComponent implements OnInit
{
  model: GetPaymentMethodsResponse;
  form: UntypedFormGroup;
  selectedIndex: number;
  setIndex: boolean = false;
  navigatedFromDynamicRouter: boolean;

  constructor(@Inject('IPaymentMethodsService') private PaymentMethodsService: IPaymentMethodsService,
    //@Inject(DEPLOY_URL) public deployUrl: string,
    private router: Router,
    private route: ActivatedRoute,
    private snackbarService: SnackBarService,
    public dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any)
  {
    this.navigatedFromDynamicRouter = this.route.snapshot?.data["NavigatedFromDynamicRouter"] ?? false;
  }

  ngOnInit()
  {
    this.form = new UntypedFormGroup({
      ClaimCode: new UntypedFormControl('', [Validators.required])
    });
    this.load();
  }

  private load()
  {
    this.model = new GetPaymentMethodsResponse();
    this.PaymentMethodsService.Get(new GetPaymentMethodsRequest()).subscribe(response =>
    {
      this.model = response;
      if (this.model.CashEQsAvailableForRedemption.length > 0)
      {
        // this.model.CashEQsAvailableForRedemption = this.model.CashEQsAvailableForRedemption.filter(x => x.Amount == 0);

        if (this.setIndex)
        {
          this.selectedIndex = 2;
        }
      }
    });
  }

  modifyCreditCard(event: string)
  {
    if (!this.navigatedFromDynamicRouter)
    {
      this.router.navigate([`../paymentmethod/` + event], { relativeTo: this.route });
    }
    else
    {
      if (event)
        this.router.navigate([`./edit/` + event], { relativeTo: this.route });
      else
        this.router.navigate([`./add`], { relativeTo: this.route });
    }
  }

  RedeemValidationErrors()
  {
    this.form.get('ClaimCode').setValidators([Validators.required]);
    this.form.get('ClaimCode').updateValueAndValidity();
  }

  Redeem(form: UntypedFormGroup)
  {
    const cliamrequest = new PostPaymentMethodsRequest();
    this.RedeemValidationErrors();
    if (form.status != "INVALID")
    {
      cliamrequest.ClaimCode = form.get('ClaimCode').value.trim();

      this.PaymentMethodsService.Post(cliamrequest).subscribe(response =>
      {
        form.get('ClaimCode').reset();
        form.get('ClaimCode').clearValidators();
        form.get('ClaimCode').updateValueAndValidity();
        if (response.SuccessMessage)
        {
          this.snackbarService.showSuccess(response.SuccessMessage);
          this.setIndex = true;
        }
        this.load();
      }, error =>
      {
        form.get('ClaimCode').setErrors({ 'cliamCodeInvalid': true, message: error?.error?.Title });
      });
    }
  }

  OnVoucherDetail(uid: string)
  {
    let request = new GetVoucherDetailsRequest();
    request.CertificateID = uid;
    this.PaymentMethodsService.GetVoucherDetails(request).subscribe((result: GetVoucherDetailsResponse) =>
    {
      const res = result.VoucherDetailInfo;

      let data = new VoucherDetail();

      if (res)
      {
        data = {
          Name: res.Description,
          Description: res.Description,
          CashEqID: res.CashEqID,
          CertID: res.CertID,
          IssuedDate: res.IssuedDate,
          ExpiryDate: res.ExpiryDate,
          OriginalBalance: res.OriginalBalance,
          CurrentBalance: res.CurrentBalance,
          IsPromo: res.IsPromo,
          CertNumRounds: res.CertNumRounds,
          MinPlayersRequired: res.MinPlayersRequired,
          numPlayers: res.numPlayers,
          CertType: res.CertType,
          RedeemableAt: res.RedeemableAt,
          RemainingRound: res.RemainingRound,
          CertificateID: res.CertificateID,
          Transactions: result.TransactionDetailsInfo,
          CourseNames: result.CourseInfo,
        };
      }

      this.dialog.open(VoucherDetailDialogComponent, {
        width: '400px',
        height: '600px',
        panelClass: 'o-dialog-sm-fullscreen',
        data
      });
    });
  }
}
