import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMyProfileFieldInfoRequest, GetMyProfileFieldInfoResponse, IMyProfileFieldInfoService, PostMyProfileFieldInfoRequest, PostMyProfileFieldInfoResponse } from '../@intermediate/my-profile-field-info';

@Injectable({
  providedIn: 'root'
})

export class MyProfileFieldInfoService implements IMyProfileFieldInfoService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/MyProfileFieldInfo';
  }

  public Get(request: GetMyProfileFieldInfoRequest): Observable<GetMyProfileFieldInfoResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMyProfileFieldInfoResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostMyProfileFieldInfoRequest): Observable<PostMyProfileFieldInfoResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMyProfileFieldInfoResponse>(apiUrl, request);
  }
}
