import { NgModule } from '@angular/core';
import { TransactionsComponent } from './transactions.component';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { DatePipe } from '@angular/common';
import { MatBottomSheetModule } from '@angular/material/bottom-sheet';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatRippleModule, MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatToolbarModule } from '@angular/material/toolbar';
import { SummaryWidgetModule } from '../../components/summarywidget/summarywidget.module';
import { TransactionsService } from '../../@services/transactions.service';
import { MaterialGenericFilterModule, DynamicFormComponent } from '@tei/material-generic-filter';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatFormFieldModule, MatIconModule, MatInputModule, MatExpansionModule, MatButtonModule, MatRippleModule, MatDatepickerModule, MatNativeDateModule, MatToolbarModule, MatSelectModule, MatGridListModule, MatListModule, MatBottomSheetModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [MaterialGenericFilterModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule, BreadCrumbModule];
@NgModule({
    declarations: [TransactionsComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: TransactionsComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...CORE_MODULES,
        SummaryWidgetModule,
    ],
    providers: [{ provide: 'ITransactionsService', useClass: TransactionsService },
        DatePipe]
})
export class TransactionsModule { }
