import { Component, OnInit, Inject } from '@angular/core';
import { ISettingsService, GetSettingsRequest, GetSettingsResponse, PostSettingsRequest, PostSettingsResponse } from '../../@intermediate/settings';

@Component({
  selector: 'my-account-pages-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit
{
  model: GetSettingsResponse;

  constructor(@Inject('ISettingsService') private settingsService: ISettingsService)
  {

  }

  ngOnInit()
  {
    const request = new GetSettingsRequest();
    this.load(request);
  }

  private load(request: GetSettingsRequest)
  {
    this.model = new GetSettingsResponse();

    this.settingsService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  private save(request: PostSettingsRequest)
  {
    this.settingsService.Post(request).subscribe(response =>
    {

    });
  }
}
