import { Component, OnInit, Inject } from '@angular/core';
import { ISummaryWidgetService, GetSummaryWidgetRequest, GetSummaryWidgetResponse, SummaryWidgetResponse } from '../../@intermediate/summarywidget';

@Component({
  selector: 'tei-my-account-summarywidget',
  templateUrl: './summarywidget.component.html',
  styleUrls: ['./summarywidget.component.scss']
})
export class SummaryWidgetComponent implements OnInit
{
  model: GetSummaryWidgetResponse;
  WidgetResponse: SummaryWidgetResponse[] = [];
  FandBMinimums: SummaryWidgetResponse[] = [];
  RewardPoints: SummaryWidgetResponse[] = [];
  FandBMinimum: SummaryWidgetResponse;
  RewardPoint: SummaryWidgetResponse;
  FandBMinimumLength: number;
  SummaryWidgetLength: number;
  RewardPointLength: number;
  WidgetResponseLength: number;
  EnableFBMinimums: boolean;
  EnableRewards: boolean;
  EnableCashEQ: boolean;
  //progress bar properties
  color: string = 'Primary-green';
  mode: string = 'determinate';
  bufferValue: number;
  dueValue: string;


  constructor(@Inject('ISummaryWidgetService') private SummaryWidgetService: ISummaryWidgetService)
  {

  }

  ngOnInit()
  {
    const request = new GetSummaryWidgetRequest();
    this.load(request);
  }



  private load(request: GetSummaryWidgetRequest)
  {
    if (!this.model)
    {
      this.model = new GetSummaryWidgetResponse();
    }

    this.SummaryWidgetService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.SummaryWidgetLength = response.summaryWidgetResponse.length;
      this.EnableFBMinimums = response.Settings.EnableFBMinimums;
      this.EnableRewards = response.Settings.EnableRewards;
      this.EnableCashEQ = response.Settings.EnableCashEQ;
      if (this.SummaryWidgetLength != 0)
      {
        this.FandBMinimums = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetFBMinimus'; });
        this.FandBMinimumLength = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetFBMinimus'; }).length;
        if (this.FandBMinimumLength != 0)
        {
          this.FandBMinimum = this.FandBMinimums[0];
          this.bufferValue = parseFloat(this.FandBMinimum.SubDisplayValue.replace(/[^0-9.-]+/g, ""));
          this.dueValue = this.FandBMinimum.SubTitle.substring(7, 18);
        }
        this.RewardPoints = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetRewards'; });
        this.RewardPointLength = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetRewards'; }).length;
        if (this.RewardPointLength != 0)
        {
          this.RewardPoint = this.RewardPoints[0];
        }
        this.WidgetResponseLength = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetCashEQ'; }).length;
        if (this.WidgetResponseLength != 0)
        {
          this.WidgetResponse = response.summaryWidgetResponse.filter(function (x) { return x.WidgetName == 'widgetCashEQ'; });
        }
      }
    });
  }
}
