import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { DeleteMyProfileFieldCategoryRequest, GetMyProfileFieldCategoryRequest, GetMyProfileFieldCategoryResponse, IMyProfileFieldCategoryService, MyProfileFieldCategory, PostMyProfileFieldCategoryRequest, PostSetCategoriesOrderRequest } from '../../@intermediate/myprofile-field-category';
import { AddMyprofilefieldCategoryComponent } from '../../components/add-myprofilefield-category/add-myprofilefield-category.component';
import { UpdateMyprofilefieldCategoryComponent } from '../../components/update-myprofilefield-category/update-myprofilefield-category.component';

@Component({
  selector: 'tei-my-account-myprofile-field-category',
  templateUrl: './myprofile-field-category.component.html',
  styleUrls: ['./myprofile-field-category.component.scss']
})
export class MyprofileFieldCategoryComponent implements OnInit
{
  isLoading = false;
  model: GetMyProfileFieldCategoryResponse;
  breadCrumbData: BreadCrumb[];

  constructor(
    @Inject('IMyProfileFieldCategoryService') private myProfileFieldCategoryService: IMyProfileFieldCategoryService,
    public dialog: MatDialog,
    private _snackBar: MatSnackBar,
    private translate: TranslateService
  ) { }

  ngOnInit(): void
  {
    this.loadCategories().then();
    this.setBreadCrumbData();
  }

  setBreadCrumbData()
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
        relativeUrl: '/office/contacts/setup'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
        relativeUrl: '/office/contacts/setup/web-account-configuration'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.PROFILE'),
        relativeUrl: "/office/contacts/setup/web-account-configuration/my-profile",
      },
      {
        title: this.translate.instant('MY_ACCOUNT.MY_PROFILE_FILED_CATEGORY.CATEGORIES'),
        relativeUrl: "",
      },

    ];
  }


  openAddDialog()
  {
    const dialogRef = this.dialog.open(AddMyprofilefieldCategoryComponent, {
      panelClass: 'category-dialog'
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.loadCategories().then();
    },
      error =>
      {
        this.openSnackBar(error, "");
      });
  }

  updateAddDialog(category: MyProfileFieldCategory)
  {
    const dialogRef = this.dialog.open(UpdateMyprofilefieldCategoryComponent, {
      data: category,
      panelClass: 'category-dialog'
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.loadCategories().then();
    },
      error =>
      {
        this.openSnackBar(error, "");
      });
  }

  openDeleteConfirmationDialog(category: MyProfileFieldCategory)
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'MY_ACCOUNT.DELETE_MYROFILE_CATEGORY.DELETE_CATEGORY',
          content: this.translate.instant('MY_ACCOUNT.DELETE_MYROFILE_CATEGORY.DO_YOU_WANT_TO_DELETE_THE_CATEGORY', { category: category.Name }),
          submitButtonLabel: 'MY_ACCOUNT.DELETE_MYROFILE_CATEGORY.DELETE',
          closeButtonLabel: 'MY_ACCOUNT.DELETE_MYROFILE_CATEGORY.CLOSE'
        }
      },
      width: '350px',
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteCategory(category).then();
      }
    },
      error =>
      {
        this.openSnackBar(error, "");
      });
  }

  async loadCategories()
  {
    this.isLoading = true;

    try
    {
      const response = await firstValueFrom(this.myProfileFieldCategoryService.Get(new GetMyProfileFieldCategoryRequest()));
      this.model = response;
    } catch (err)
    {
      console.log(err);

    } finally
    {
      this.isLoading = false;
    }

    // var isFirefox = navigator.userAgent.toLowerCase().indexOf('firefox') > -1;
    // if (isFirefox && this.model.MyProfileFieldCategories[0].OrderNo != 1)
    // {
    //   // this.model.MyProfileFieldCategories.sort((a, b) => a.OrderNo > b.OrderNo ? -1 : a.OrderNo < b.OrderNo ? 1 : 0);
    //   // console.log("this.model.MyProfileFieldCategories");
    //   // console.log(this.model.MyProfileFieldCategories);
    // }

  }

  async deleteCategory(category: MyProfileFieldCategory)
  {
    let request = new DeleteMyProfileFieldCategoryRequest();
    request.FieldGroupID = category.FieldGroupID;
    this.isLoading = true;
    try
    {
      await firstValueFrom(this.myProfileFieldCategoryService.Delete(request));
      this.loadCategories().then();
    } catch (err)
    {
      console.log(err);
    } finally
    {
      this.isLoading = false;
    }

  }

  async updateCategory(category: MyProfileFieldCategory)
  {
    let request = new PostMyProfileFieldCategoryRequest();
    request.FieldGroupID = category.FieldGroupID;
    this.isLoading = true;

    try
    {
      await firstValueFrom(this.myProfileFieldCategoryService.Update(request));
      this.loadCategories().then();

    } catch (err)
    {
      console.log(err);

    } finally
    {
      this.isLoading = false;
    }
  }

  setCategoryOrder(categories: MyProfileFieldCategory[])
  {
    categories.forEach((category, index) =>
    {
      category.OrderNo = (index + 1);
    });
  }

  async saveCategoryOrder(categories: MyProfileFieldCategory[])
  {
    let request = new PostSetCategoriesOrderRequest();
    request.Catetgories = categories;
    this.isLoading = true;

    try
    {
      await firstValueFrom(this.myProfileFieldCategoryService.SetCategoriesOrder(request));
      this.loadCategories().then();
    } catch (err)
    {
      console.log(err);
    } finally
    {
      this.isLoading = false;
    }
  }

  drop(event: CdkDragDrop<string[]>)
  {
    if (this.model.MyProfileFieldCategories[event.previousIndex].IsSystem ||
      this.model.MyProfileFieldCategories[event.currentIndex].IsSystem)
    {
      return;
    }
    moveItemInArray(this.model.MyProfileFieldCategories, event.previousIndex, event.currentIndex);
    this.setCategoryOrder(this.model.MyProfileFieldCategories);
    this.saveCategoryOrder(this.model.MyProfileFieldCategories).then();
  }

  openSnackBar(message: string, action: string)
  {
    this._snackBar.open(message, action, {
      duration: 3000,
    });
  }
}
