import { NgModule } from '@angular/core';
import { SelectAddressComponent } from './select-address.component';
import { SelectAddressService } from '../../@services/select-address.service';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ManageAddressModule } from '../manage-address/manage-address.module';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const BASE_MODULES = [CommonModule, FormsModule ];
const MATERIAL_MODULES = [
  MatFormFieldModule,
  MatSelectModule,
  MatInputModule,
  MatCardModule,
  MatButtonModule,
  MatButtonToggleModule,
  MatIconModule];
const TEI_MATERIAL_MODULES = [ManageAddressModule];

const COMPONENTS = [SelectAddressComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'ISelectAddressService', useClass: SelectAddressService }],
})
export class SelectAddressModule { }
