import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import
  {
    GetManageAddressRequest,
    GetManageAddressResponse, IManageAddressService, PostManageAddressRequest,
    PostManageAddressResponse
  } from '../@intermediate/manage-address';

@Injectable({
  providedIn: 'root'
})

export class ManageAddressService implements IManageAddressService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + 'ManageAddress';
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/ManageAddress';
  }

  public Get(request: GetManageAddressRequest): Observable<GetManageAddressResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageAddressResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageAddressRequest): Observable<PostManageAddressResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageAddressResponse>(apiUrl, request);
  }
}
