import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import
{
  GetPaymentMethodRequest,
  GetPaymentMethodResponse, IPaymentMethodService, PostPaymentMethodRequest,
  PostPaymentMethodResponse
} from '../@intermediate/paymentmethod';

@Injectable({
  providedIn: 'root'
})

export class PaymentMethodService implements IPaymentMethodService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + 'PaymentMethod';
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/PaymentMethod';
  }

  public Get(request: GetPaymentMethodRequest): Observable<GetPaymentMethodResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPaymentMethodResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPaymentMethodRequest): Observable<PostPaymentMethodResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPaymentMethodResponse>(apiUrl, request);
  }
}
