import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { FieldOption, GetMyProfileFieldChooserRequest, GetMyProfileFieldChooserResponse, IMyProfileFieldChooserService, MasterField, PostMyProfileFieldChooserRequest } from '../../@intermediate/myprofile-field-chooser';

@Component({
  selector: 'tei-my-account-field-chooser',
  templateUrl: './field-chooser.component.html',
  styleUrls: ['./field-chooser.component.css']
})
export class FieldChooserComponent implements OnInit
{
  model: GetMyProfileFieldChooserResponse;
  selectedCategory: string;
  allFieldChecked: boolean = false;
  showFieldChooserSection = true;
  numberOfSelectedField: number = 0;
  isValid = false;

  constructor(@Inject('IMyProfileFieldChooserService') private fieldChooserService: IMyProfileFieldChooserService,
    private dialogRef: MatDialogRef<FieldChooserComponent>, private _snackBar: MatSnackBar) { }

  ngOnInit(): void
  {
    this.fieldChooserService.Get(new GetMyProfileFieldChooserRequest()).subscribe(response =>
    {
      this.model = response;
      this.selectedCategory = this.model.Categories[0];
      this.changeCategory(this.selectedCategory);
      this.numberOfSelectedField = this.model?.Fields.filter(x => x.IsSelected).length;
    });
  }

  changeCategory(category: string)
  {
    this.selectedCategory = category;
    this.model.Fields.forEach(x =>
    {
      if (x.CategoryName == category)
      {
        x.Display = true;
      } else
      {
        x.Display = false;
      }
    });
    this.checkSelectedAll();
  }

  updateAllComplete()
  {
    this.allFieldChecked = this.model?.Fields != null && this.model?.Fields.filter(x => x.CategoryName == this.selectedCategory).every(t => t.IsSelected);
    this.numberOfSelectedField = this.model?.Fields.filter(x => x.IsSelected).length;
  }

  someComplete()
  {
    if (this.model?.Fields == null)
    {
      return false;
    }
    const selectedFieldsCount = this.model?.Fields.filter(x => x.CategoryName == this.selectedCategory && x.IsSelected == true).length;
    const categoryFieldCount = this.model?.Fields.filter(x => x.CategoryName == this.selectedCategory).length;

    return selectedFieldsCount > 0 && !this.allFieldChecked;
  }

  setAll(completed: boolean)
  {
    this.allFieldChecked = completed;
    if (this.model.Fields == null)
    {
      return;
    }

    this.model.Fields.forEach(x =>
    {
      if (x.CategoryName == this.selectedCategory && !x.IsSystem)
      {
        x.IsSelected = completed;
      }
    });

    this.numberOfSelectedField = this.model?.Fields.filter(x => x.IsSelected).length;
  }

  checkSelectedAll()
  {
    if (this.model.Fields == null)
    {
      return false;
    }

    this.allFieldChecked = this.model.Fields.filter(x => x.CategoryName == this.selectedCategory).every(x => x.IsSelected == true);
  }

  goToCategorySection(display: boolean)
  {
    if (!display)
    {
      this.model.Fields.sort(function (a, b)
      {
        return a.FieldGroupID - b.FieldGroupID;
      });
    } else
    {
      if (display)
      {
        this.model.Fields.sort(function (a, b)
        {
          return a.OrderID - b.OrderID;
        });
      }
    }

    this.showFieldChooserSection = display;
  }

  save()
  {
    if (!this.validation())
    {
      this.openSnackBar("All fields should be mapped with a category", "");
      return;
    }

    let request = new PostMyProfileFieldChooserRequest();
    this.model.Fields.forEach(x =>
    {
      let masterField = new MasterField();
      masterField.FieldGroupID = x.FieldGroupID;
      masterField.IsDeleted = !x.IsSelected;
      masterField.Label = x.UDFName;
      masterField.Title = x.UDFName;
      masterField.UDFID = x.UDFUID;
      masterField.MasterFieldTypeID = 4;
      masterField.UDFType = x.UDFType;
      masterField.UDFFieldFormat = x.UDFFieldFormat;
      masterField.MasterFieldFormatID = x.MasterFieldFormatID;
      masterField.SubRefID = x.CategoryName;
      request.MasterFields.push(masterField);
    });

    this.model.FieldOptions.forEach(x =>
    {
      let option = new FieldOption();
      option.UDFUID = x.UDFUID;
      option.Name = x.Name;
      request.FieldOptions.push(option);
    });

    this.fieldChooserService.Post(request).subscribe(response =>
    {
    });
    this.closeDialog();
  }

  validation()
  {
    return this.model.Fields.filter(x => x.IsSelected && x.FieldGroupID == 0).length == 0;
  }

  closeDialog()
  {
    this.dialogRef.close();
  }

  openSnackBar(message: string, action: string)
  {
    this._snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }
}
