import
{
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Inject,
  OnDestroy,
  OnInit,
  Pipe,
  PipeTransform,
  ReflectiveInjector,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatMenuTrigger } from '@angular/material/menu';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { IDiningState } from '@tei/booking-dining-states';
import { ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { DynamicFormService } from '@tei/material-generic-filter';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { take } from "rxjs/operators";
import
{
  GetItineraryRequest,
  GetItineraryResponse, IItineraryService,
  ItineraryItem,
  ItineraryItemGroup,
  PostCancelReservationRequest
} from '../../@intermediate/itinerary';
import { GetItineraryFilterRequest, GetItineraryFilterResponse, ItineraryFilter, ItineraryFilterDialogData, ItineraryFilterResult } from '../../@intermediate/itinerary-filter/models/get-itinerary-filter.model';
import { ItineraryFilterService } from '../../@services/itinerary-filter.service';
import { ItineraryFilterDialogComponent } from '../../components/itinerary-filter-dialog/itinerary-filter-dialog.component';
import { ItineraryFilterComponent } from '../../components/itinerary-filter/itinerary-filter.component';

@Component({
  selector: 'my-account-pages-itinerary',
  templateUrl: './itinerary.component.html',
  styleUrls: ['./itinerary.component.scss']
})
export class ItineraryComponent implements OnInit, OnDestroy
{
  @ViewChild('placeHolder', { read: ViewContainerRef }) private _placeHolder: ViewContainerRef;
  @ViewChild('filterMenuContent') itineraryFilterComponent: ItineraryFilterComponent;
  model: GetItineraryResponse;
  itineraryList: Pick<GetItineraryResponse, "ItineraryGroups">;
  filtermodel: GetItineraryFilterResponse;
  pageLength: number;
  filterTodayDate: moment.Moment = moment();
  formValues: any[] = [];
  showFilter: boolean = true;
  loadedOnInit: boolean = false;
  isLoading: boolean = false;
  isShowingUpcoming = true;
  isFilterPanelOpen = false;
  filter = new ItineraryFilter();
  upcomingFilter = new ItineraryFilter();
  pastFilter = new ItineraryFilter(false);
  isMobile = false;
  isFilterDisabled = false;
  allTypes: string[] = [];

  subscription = new Subscription();

  DINING_RESERVATION_STATUS_CANCELLED = 3;

  constructor(@Inject('IItineraryService') private ItineraryService: IItineraryService,
    private dynamicfilter: DynamicFormService,
    private snackBar: SnackBarService,
    private responsiveService: ResponsiveService,
    public dialog: MatDialog,
    private itineraryFilterService: ItineraryFilterService,
    private teiTextTranslateService: TranslateService,
    private _cmpFctryRslvr: ComponentFactoryResolver,
    private router: Router,
    private store: Store<IDiningState>)
  {

  }

  ngOnInit()
  {
    this.model = new GetItineraryResponse();
    this.itineraryList = { ItineraryGroups: [] };
    this.dataonload();
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobile = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }


  dataonload()
  {
    this.ItineraryService.GetItineraryFilter(new GetItineraryFilterRequest()).pipe(take(1)).subscribe(response =>
    {
      //response.FilterConditions.filter(x => x.name == "daterange")[0].value = { startDate: this.filterTodayDate, endDate: null };
      response.FilterConditions.filter(x => x.name1 == "startdaterange")[0].value1 = this.filterTodayDate;
      response.FilterConditions.filter(x => x.name2 == "enddaterange")[0].value2 = null;
      // this.showFilter = false;

      //Added to hide typeOfBooking filter temporary
      // response.FilterConditions = response.FilterConditions.filter(item => item.name != "typeOfBooking");

      this.filtermodel = response;
      this.loadData();
    });
  }

  loadData(): void
  {
    const request = new GetItineraryRequest();
    if (this.isShowingUpcoming)
    {
      request.StartDate = this.convertToLocaleDateString(new Date());
    } else
    {
      request.StartDate = this.convertToLocaleDateString(this.filter.startDate);
      request.EndDate = this.convertToLocaleDateString(this.filter.endDate);
    }
    this.load(request);
    this.loadedOnInit = true;
  }

  onTabChange(event: MatTabChangeEvent): void
  {
    this.itineraryFilterComponent?.tabChanged();
    switch (event.index)
    {
      case 0:
        this.isShowingUpcoming = true;
        break;
      case 1:
        this.isShowingUpcoming = false;
        break;
      default:
        break;
    }
    if (this.isShowingUpcoming)
    {
      this.pastFilter = this.filter;
      this.filter = this.upcomingFilter;
    } else
    {
      this.upcomingFilter = this.filter;
      this.filter = this.pastFilter;
    }
    this.loadData();
  }

  onFilterDropDownClick(menuTrigger: MatMenuTrigger): void
  {
    if (this.isFilterDisabled)
    {
      return;
    }
    if (!this.isMobile)
    {
      menuTrigger.openMenu();
      this.isFilterPanelOpen = true;
      this.filterItineraryItems(true);
    } else
    {
      const dialogSubscription = new Subscription();
      const dialog = this.dialog.open(ItineraryFilterDialogComponent, {
        panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
        width: '460px',
        data: {
          FilterConditions: this.filtermodel?.FilterConditions,
          hideDateRange: this.isShowingUpcoming,
          itineraryFilter: this.filter,
          allTypes: this.allTypes
        } as ItineraryFilterDialogData
      });
      dialogSubscription.add(this.itineraryFilterService.data$.subscribe(this.onFilterSubmit));
      dialog.afterClosed().subscribe(result =>
      {
        dialogSubscription.unsubscribe();
        if (result)
        {
          this.onFilterSubmit(result);
        }
      });
    }
  }

  onFilterDropDownClose(): void
  {
    this.isFilterPanelOpen = false;
  }

  onFilterSubmit(result: ItineraryFilterResult): void
  {
    const isDateUntouched = (this.filter?.startDate && this.filter?.endDate && !this.isShowingUpcoming) ? moment(this.filter.startDate).isSame(result.startDate) && moment(this.filter.endDate).isSame(result.endDate) : true;
    this.filter = result;
    if (isDateUntouched)
    {
      this.filterItineraryItems(false);
    } else
    {
      if (!result.isInitialSubmit)
      {
        this.loadData();
      }
    }
  }

  convertToLocaleDateString(value: Date)
  {
    return (value.getMonth() + 1).toString() + '/' + value.getDate().toString() + '/' + value.getFullYear().toString();
  }

  private load(request: GetItineraryRequest)
  {
    localStorage.setItem("ItineraryURL", window.location.href);
    this.isLoading = true;

    this.ItineraryService.Get(request).pipe(take(1)).subscribe(response =>
    {
      this.model = response;
      this.showFilter = true;
      this.model.ItineraryGroups.filter(x => x.ItineraryItems.forEach(y =>
      {
        if (y.ImageUrl == "golf_course")
        {
          y.ManageURL = location.protocol + '//' + location.host + y.ManageURL + "/true/";
        }
      }));
      this.allTypes = [];
      //It is just for TEI Events
      this.model.ItineraryGroups.forEach(ig =>
      {
        ig.ItineraryItems.forEach(ii =>
        {
          this.allTypes.push(ii.Type);
          if (ii.Type === 'Events')
          {
            ii.MemberDetails = ii.MemberDetails.map(md => ({
              ...md,
              Subtitle: `${ md.Subtitle }${ md.Subtitle ? ' ' : '' } ${ ii.PaidStatus ? this.teiTextTranslateService.instant('MY_ACCOUNT.ITINERARY.PREPAID') : '' }`
            })
            );
          }
        }
        );
      });
      this.allTypes = [...new Set(this.allTypes)];
      this.isLoading = false;
      if (this.isShowingUpcoming && this.allTypes.length === 0)
      {
        this.isFilterDisabled = true;
      } else
      {
        this.isFilterDisabled = false;
      }
      this.filterItineraryItems(true);
    }, error =>
    {
      this.isLoading = false;
    });
  }

  filterItineraryItems(shouldRerender: boolean): void
  {
    if (shouldRerender)
    {
      this.itineraryFilterComponent?.renderFilters(this.allTypes);
    }
    if (this.filter.typeOfBookings.length > 0 || this.filter.deselectedTypeOfBookings.length > 0)
    {
      this.itineraryList.ItineraryGroups = this.model.ItineraryGroups.map(m => ({ ...m, ItineraryItems: m.ItineraryItems.filter(f => this.filter.typeOfBookings.includes(f.Type)) }));
      this.itineraryList.ItineraryGroups = this.itineraryList.ItineraryGroups.filter(f => f.ItineraryItems.length > 0);
    } else
    {
      this.itineraryList.ItineraryGroups = this.model.ItineraryGroups;
    }
    this.pageLength = this.itineraryList.ItineraryGroups.length;
  }

  public createComponent<T>(vCref: ViewContainerRef, type: Type<T>): ComponentRef<T>
  {

    let factory = this._cmpFctryRslvr.resolveComponentFactory(type);

    // vCref is needed cause of that injector..
    let injector = ReflectiveInjector.fromResolvedProviders([], vCref.parentInjector);

    // create component without adding it directly to the DOM
    let comp = factory.create(injector);

    return comp;
  }

  cancelDiningReservation(itineraryItem: ItineraryItem): void
  {
    if (!itineraryItem)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    if (itineraryItem.Type !== 'OpenTable')
    {
      //this should not happen. so if we have this situation something is wrong
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      //Nothing
      return;
    }

    if (!itineraryItem.ReservationID)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    var request =
      {
        ReservationID: itineraryItem.ReservationID
      } as PostCancelReservationRequest;

    this.ItineraryService.CancelDiningReservation(request).subscribe(
      response =>
      {
        if (response.IsSuccess)
        {
          this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.RESERVATION_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');

          itineraryItem.IsReservationCancelled = true;
        }
        else
        {
          this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.RESERVATION_NOT_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');
        }
      },
      error =>
      {
        this.snackBar.showInfo(error.error.title, '', null, 'center', 'bottom');
      });
  }

  checkActiveItemsOnSpecificDay(itineraryItemGroup: ItineraryItemGroup)
  {
    return itineraryItemGroup.ItineraryItems.filter(item => !item.IsReservationCancelled).length > 0 ? true : false;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}


@Pipe({ name: 'safeHtml' })
export class Safe implements PipeTransform
{
  constructor(private sanitizer: DomSanitizer) { }

  transform(style: string)
  {
    style = style.replace(/&lt;/g, '<').replace(/&gt;/g, '>');
    return this.sanitizer.bypassSecurityTrustHtml(style);
  }
}
