import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
//TODO refactor Address class to @tei/common-my-account-widgets
import { Address, GetAddressesRequest, GetAddressesResponse, IAddressesService, PostAddressesRequest, PostAddressesResponse } from '../../@intermediate/addresses';

@Component({
  selector: 'my-account-pages-addresses',
  templateUrl: './addresses.component.html',
  styleUrls: ['./addresses.component.scss']
})

export class AddressesComponent implements OnInit
{
  model: GetAddressesResponse;
  mySubscription: any;
  navigatedFromDynamicRouter: boolean;
  constructor(
    @Inject('IAddressesService') private addressesService: IAddressesService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService,
    private translateService: TranslateService
  )
  {
    this.navigatedFromDynamicRouter = this.activatedRoute.snapshot?.data["NavigatedFromDynamicRouter"] ?? false;
  }

  ngOnInit()
  {
    const request = new GetAddressesRequest();
    this.load(request);
  }

  private load(request: GetAddressesRequest)
  {
    this.model = new GetAddressesResponse();

    this.addressesService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  private save(request: PostAddressesRequest)
  {
    let resp = new PostAddressesResponse();
    this.addressesService.Post(request).subscribe(response =>
    {
      resp = response;
      if (response.IsSuccessful)
      {
        this.ngOnInit();
        this.snackbarService.showSuccess(this.translateService.instant('MY_ACCOUNT.ADDRESSES.ADDRESS_DELETED'));
      }
      else
      {
        this.snackbarService.showError(this.translateService.instant('MY_ACCOUNT.ADDRESSES.DELETION_FAILD'));
        // Need some kind of messaging to let the user know what happened
      }
    });
  }

  deleteAddressAction(AddressID: string)
  {
    const dialogData: DialogData = {
      title: this.translateService.instant('MY_ACCOUNT.ADDRESSES.DELETE'),
      message: this.translateService.instant('MY_ACCOUNT.ADDRESSES.ARE_YOU_SURE_YOU_WANT_TO_DELETE_THIS_ADDRESS'),
      textOkayButton: this.translateService.instant('MY_ACCOUNT.ADDRESSES.DELETE'),
      buttonColor: 'warn',
      textCancelButton: this.translateService.instant('MY_ACCOUNT.ADDRESSES.CANCEL')
    };

    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new PostAddressesRequest();
        request.AddressID = AddressID;
        this.save(request);
      }
    });
  }

  get filterByIsDisplayed()
  {
    let temp: Array<Address>;
    if (this.model.Addresses)
    {
      temp = this.model.Addresses.filter(x => x.isDisplayed === 'True');
    }
    return temp;
  }
}


