import { CommonModule } from '@angular/common';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MaterialGenericFilterModule } from '@tei/material-generic-filter';
import { ItineraryService } from '../../@services/itinerary.service';
import { ApiProviderLogoSvgModule } from '../../components/api-provider-logo-svg/api-provider-logo-svg.module';
import { ItineraryConfirmationDialogModule } from '../../components/itinerary-confirmation-dialog/itinerary-confirmation-dialog.module';
import { ItineraryFilterDialogModule } from '../../components/itinerary-filter-dialog/itinerary-filter-dialog.module';
import { ItineraryFilterModule } from '../../components/itinerary-filter/itinerary-filter.module';
import { ItineraryItemModule } from '../../components/itinerary-item/itinerary-item.module';
import { ItineraryComponent, Safe } from './itinerary.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [
  MatCardModule,
  MatExpansionModule,
  MatButtonModule,
  MatIconModule,
  MatTabsModule,
  MatInputModule,
  MatMenuModule,
  MatFormFieldModule,
  MatDividerModule];
const MODULES = [
  MaterialGenericFilterModule,
  ItineraryConfirmationDialogModule,
  ItineraryFilterDialogModule,
  ItineraryItemModule,
  ItineraryFilterModule,
  ApiProviderLogoSvgModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule];
@NgModule({
  declarations: [ItineraryComponent, Safe],
  imports: [
    RouterModule.forChild([{ path: '', component: ItineraryComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IItineraryService', useClass: ItineraryService }],
  exports: [Safe],
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA
  ],
})
export class ItineraryModule { }
