import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetStatementURLRequest, GetStatementURLResponse } from '../@intermediate/statement-url/models/get-statement-url.model';
import { GetStatementsRequest, GetStatementsResponse, IStatementsService, PostStatementsRequest, PostStatementsResponse } from '../@intermediate/statements';

@Injectable({
  providedIn: 'root'
})

export class StatementsService implements IStatementsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Statements';
  }

  public Get(request: GetStatementsRequest): Observable<GetStatementsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetStatementsResponse>(apiUrl, { params: request as any });
  }

  public GetStatementURL(request: GetStatementURLRequest): Observable<GetStatementURLResponse>
  {
    const apiUrl = this.serviceUrl + '/GetStatementsURL';
    return this.http.get<GetStatementURLResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostStatementsRequest): Observable<PostStatementsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostStatementsResponse>(apiUrl, request);
  }
}
