import { AfterViewInit, Component, Inject, ViewChild } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ItineraryFilterDialogData, ItineraryFilterResult } from '../../@intermediate/itinerary-filter/models/get-itinerary-filter.model';
import { ItineraryFilterService } from '../../@services/itinerary-filter.service';
import { ItineraryFilterComponent } from '../itinerary-filter/itinerary-filter.component';

@Component({
  selector: 'tei-itinerary-filter-dialog',
  templateUrl: './itinerary-filter-dialog.component.html',
  styleUrls: ['./itinerary-filter-dialog.component.scss']
})
export class ItineraryFilterDialogComponent implements AfterViewInit
{
  @ViewChild('filterMenuContent') itineraryFilterComponent: ItineraryFilterComponent;
  constructor(
    public dialog: MatDialogRef<ItineraryFilterDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ItineraryFilterDialogData,
    private itineraryFilterService: ItineraryFilterService
  ) { }

  onClose()
  {
    this.dialog.close();
  }

  onFilterSubmit(result: ItineraryFilterResult): void
  {
    if (result.isInitialSubmit)
    {
      this.itineraryFilterService.setData(result);
    } else
    {
      this.dialog.close(result);
    }
  }

  ngAfterViewInit(): void
  {
    this.itineraryFilterComponent.renderFilters(this.data.allTypes);
  }
}