import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { FieldGroup } from './get-my-profile-field-info.model';
import { PostFieldGroupLookupRequest } from '@tei/common-field-management-shared';

export class PostMyProfileFieldInfoRequest
{
  MyProfileFieldInfos: PostFieldGroupLookupRequest[];
}

export class PostMyProfileFieldInfoResponse
{

}

export class FieldGroupForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      FieldGroupID: new UntypedFormControl(),
      FieldGroupAnswerID: new UntypedFormControl(),
      Name: new UntypedFormControl(),
    });
  }

  public setValue(value: FieldGroup)
  {
    this.patchValue({
      FieldGroupID: value.FieldGroupID,
      FieldGroupAnswerID: value.FieldGroupAnswerID,
      Name: value.Name,
    });
  }
}
