import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { MatLegacySelectChange as MatSelectChange } from '@angular/material/legacy-select';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import
{
  GetManageAddressRequest,
  GetManageAddressResponse,
  IManageAddressService,
  PostManageAddressRequestForm
} from '../../@intermediate/manage-address';
import { Address } from '../../@intermediate/manage-address/models/address.model';
import { Country } from '../../@intermediate/manage-address/models/country.model';
import { Region } from '../../@intermediate/manage-address/models/region.model';

@Component({
  selector: 'tei-my-account-widgets-manage-address',
  templateUrl: './manage-address.component.html',
  styleUrls: ['./manage-address.component.scss']
})
export class ManageAddressComponent implements OnInit
{
  // INPUT PARAMETERS
  @Input() addressID: string;
  @Input() showSaveAddress: string;

  // OUTPUT PARAMETERS
  @Output() activeAddress = new EventEmitter<Address>();

  // DEFAULT PARAMETERS
  model: GetManageAddressResponse;
  form: PostManageAddressRequestForm;
  countries: Array<Country>;
  regions: Array<Region>;
  bSaveAddress: boolean;

  constructor(@Inject('IManageAddressService')
  private ManageAddressService: IManageAddressService,
    private snackbarService: SnackBarService,
    private translateService: TranslateService) { }

  ngOnInit()
  {
    this.form = new PostManageAddressRequestForm();
    const request = new GetManageAddressRequest();
    request.AddressID = this.addressID ? this.addressID : '';
    if (this.showSaveAddress)
    {
      this.bSaveAddress = this.showSaveAddress === 'true' ? true : false;
    }
    this.load(request);
  }

  private load(request: GetManageAddressRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageAddressResponse();
    }
    this.ManageAddressService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.countries = this.model.Countries;
      this.regions = new Array<Region>();
      if (this.model.Address)
      {
        const event = new MatSelectChange(null, this.model.Address.CountryCode);
        this.countryChangeAction(event);
        this.form.setValue(this.model);
      }
    });
  }

  save(form: PostManageAddressRequestForm)
  {
    if (this.form.valid)
    {
      if (this.showSaveAddress === 'false')
      {
        form.value.isDisplayed = 'True';
      }
      else if (form.value.isDisplayed === '')
      {
        form.value.isDisplayed = 'False';
      }
      this.ManageAddressService.Post(form.value).subscribe(response =>
      {
        if (this.addressID)
        {
          this.snackbarService.showSuccess(this.translateService.instant('MY_ACCOUNT.ADDRESSES.ADDRESS_EDITED'));

        } else
        {
          this.snackbarService.showSuccess(this.translateService.instant('MY_ACCOUNT.ADDRESSES.ADDRESS_CREATED'));
        }
        this.activeAddress.emit(response.Address);
      });
    }
  }

  countryChangeAction(event: MatSelectChange): void
  {
    this.regions = this.countries.find(n => n.CountryCode === event.value)?.Regions;
    if (this.regions?.length === 1)
    {
      this.form.get('RegionCode').setValue(this.regions[0].RegionCode);
    }
  }

  onCancel()
  {
    this.activeAddress.emit(this.model.Address ? this.model.Address : new Address());
  }
}
