import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { FormlyModule } from "@ngx-formly/core";
import { FormlyMaterialModule } from "@ngx-formly/material-legacy";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import
{
  FormlyCustomSettingCardWrapperModule,
  FormlyCustomSettingCheckBoxModule,
  FormlyCustomSettingDatePickerModule,
  FormlyCustomSettingLabelModule,
  FormlyCustomSettingRowWrapperModule,
  FormlyCustomSettingSelectModule,
  FormlyCustomSettingTextModule,
  FormlyCustomSettingToggleModule,
} from "@tei/formly-custom-components";
import { BreadCrumbModule, LoadingModule } from "@tei/material";
import { TransactionSettingsService } from "../../@services/transcation-settings.service";
import { TransactionSettingsComponent } from "./transaction-settings.component";
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
  FormlyMaterialModule,
  FormlyModule,
];
const MATERIAL_MODULES = [
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule,
  FormlyCustomSettingSelectModule,
  FormlyCustomSettingCardWrapperModule,
  FormlyCustomSettingTextModule,
  FormlyCustomSettingLabelModule,
  FormlyCustomSettingToggleModule,
  FormlyCustomSettingDatePickerModule,
  FormlyCustomSettingRowWrapperModule,
  FormlyCustomSettingCheckBoxModule,
];
const COMPONENTS = [TransactionSettingsComponent];

const CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES,
  ],

  providers: [
    { provide: "ITransactionSettingsService", useClass: TransactionSettingsService },
  ],
})
export class TransactionSettingsModule { }
