import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyProgressBarModule as MatProgressBarModule } from '@angular/material/legacy-progress-bar';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SummaryWidgetService } from '../../@services/summarywidget.service';
import { SummaryWidgetComponent } from './summarywidget.component';
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatCardModule, MatProgressBarModule, MatListModule, MatIconModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [SummaryWidgetComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule,TeiCurrencyTranslateModule, TeiDateTranslateModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES

  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS,
  ],
  providers: [{ provide: 'ISummaryWidgetService', useClass: SummaryWidgetService }],
})
export class SummaryWidgetModule { }
