    FormlyCustomSettingCardWrapperModule,
    FormlyCustomSettingCheckBoxModule,
    FormlyCustomSettingDatePickerModule,
    FormlyCustomSettingLabelModule,
    FormlyCustomSettingRowWrapperModule,
    FormlyCustomSettingSelectModule,
