import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetMyProfileFieldChooserRequest, GetMyProfileFieldChooserResponse, IMyProfileFieldChooserService, PostMyProfileFieldChooserRequest, PostMyProfileFieldChooserResponse } from "../@intermediate/myprofile-field-chooser";


@Injectable({
  providedIn: 'root'
})
export class MyProfileFieldChooserService implements IMyProfileFieldChooserService
{

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/MyProfileFieldChooser';
  }

  Post(request: PostMyProfileFieldChooserRequest): Observable<PostMyProfileFieldChooserResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMyProfileFieldChooserResponse>(apiUrl, request);
  }

  Get(request: GetMyProfileFieldChooserRequest): Observable<GetMyProfileFieldChooserResponse>
  {
    const apiUrl = this.serviceUrl + '/GetMyProfileFieldChooser';
    return this.http.get<GetMyProfileFieldChooserResponse>(apiUrl, request);
  }
}
