import { Component, OnInit } from "@angular/core";
import { FieldType } from "@ngx-formly/core";

@Component({
  selector: "formly-custom-setting-select",
  styleUrls: ["./formly-custom-setting-select.component.scss"],
  template: ` <div class="a-text-label">{{ to.inputLabel }}</div>
    <mat-form-field class="no-label a-dropdown" appearance="outline">
      <mat-select
        [id]="id"
        [formControl]="formControl"
        [formlyAttributes]="field"
        [required]="to.required"
      >
        <mat-option *ngFor="let item of to.options" [value]="item.value">{{
          item.label
        }}</mat-option>
      </mat-select>
    </mat-form-field>`,
})
export class FormlyCustomSettingSelect extends FieldType implements OnInit {
  ngOnInit(): void {}
}
