import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { DialogData } from '@tei/material';
import { PostChangePasswordDataRequest } from '../../@intermediate/account-security';
import { IAccountSecurityService } from '../../@intermediate/account-security/account-security.interface';
import { ChangePinComponent } from '../change-pin/change-pin.component';

@Component({
  selector: 'tei-my-account-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit
{

  form = new UntypedFormGroup({
    OldPassword: new UntypedFormControl('', Validators.required),
    NewPassword: new UntypedFormControl('', Validators.required),
    ConfirmPassword: new UntypedFormControl('', Validators.required),
  });

  constructor(
    public dialogRef: MatDialogRef<ChangePasswordComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData, @Inject('IAccountSecurityService') private AccountSecurityService: IAccountSecurityService, private snackBar: MatSnackBar) { }

  ngOnInit(): void
  {

  }

  save(form: UntypedFormGroup)
  {
    let request = new PostChangePasswordDataRequest();
    request.OldPassword = this.form.get('OldPassword').value;
    request.NewPassword = this.form.get('NewPassword').value;
    request.ConfirmPassword = this.form.get('ConfirmPassword').value;

    if (!this.validation(request))
    {
      return;
    }

    if (this.form.valid)
    {
      this.AccountSecurityService.PostChangePassword(request).subscribe(response =>
      {
        this.openSnackBar("Password changed successfully.", "");
        this.dialogRef.close();
      },
        error =>
        {
          this.openSnackBar(error.error, "");
        });
    }
  }

  openSnackBar(message: string, action: string)
  {
    this.snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }

  onCancel()
  {
    this.dialogRef.close();
  }

  validation(request: PostChangePasswordDataRequest)
  {
    let result: boolean = true;

    if (request.NewPassword != request.ConfirmPassword)
    {
      this.openSnackBar("Passwords didnt match.", "");
      result = false;
    }

    if (request.NewPassword == request.OldPassword)
    {
      this.openSnackBar("Failed. Cannot reuse current password.", "");
      result = false;
    }

    return result;
  }
}
