import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTransactionSettingsRequest, GetTransactionSettingsResponse } from '../@intermediate/transaction-settings/models/get-transaction-setting.model';
import { PostTransactionSettingsRequest, PostTransactionSettingsResponse } from '../@intermediate/transaction-settings/models/post-transaction-setting.model';
import { ITransactionSettingsService } from '../@intermediate/transaction-settings/transaction-setting.interface';

@Injectable({
  providedIn: 'root'
})

export class TransactionSettingsService implements ITransactionSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/PaymentMethods/';
  }

  public Get(request: GetTransactionSettingsRequest): Observable<GetTransactionSettingsResponse>
  {
    const apiUrl =this.serviceUrl +'GetMyTransactionsSettings';
    return this.http.get<GetTransactionSettingsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTransactionSettingsRequest): Observable<PostTransactionSettingsResponse>
  {
    const apiUrl = this.serviceUrl +'PostMyTransactionsSettings'
    return this.http.post<PostTransactionSettingsResponse>(apiUrl, request);
  }
}
