import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { GetMyProfileFieldCategoryRequest, IMyProfileFieldCategoryService, PostMyProfileFieldCategoryRequest } from '../../@intermediate/myprofile-field-category';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { firstValueFrom } from 'rxjs';

@Component({
  selector: 'tei-my-account-add-myprofilefield-category',
  templateUrl: './add-myprofilefield-category.component.html',
  styleUrls: ['./add-myprofilefield-category.component.css']
})
export class AddMyprofilefieldCategoryComponent implements OnInit
{
  isLoading = false;
  constructor(public dialogRef: MatDialogRef<AddMyprofilefieldCategoryComponent>,
    @Inject('IMyProfileFieldCategoryService') private myProfileFieldService: IMyProfileFieldCategoryService) { }

  formGroup: UntypedFormGroup;
  name: string;
  request: PostMyProfileFieldCategoryRequest;

  ngOnInit(): void
  {
    this.request = new PostMyProfileFieldCategoryRequest();
  }

  onSubmit()
  {
    this.myProfileFieldService.Get(new GetMyProfileFieldCategoryRequest()).subscribe(async listOfCategoriesResponse =>
    {
      this.request.OrderNo = listOfCategoriesResponse.MyProfileFieldCategories.length + 1;
      try
      {
        this.isLoading = true;
        await firstValueFrom(this.myProfileFieldService.Post(this.request));
        this.dialogRef.close(true);
      } catch (err)
      {
        console.log(err);
        this.dialogRef.close(false);
      } finally
      {
        this.isLoading = false;
      }
    });
  }

  closeDialog(): void
  {
    this.dialogRef.close(false);
  }
}
