import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSummaryWidgetRequest, GetSummaryWidgetResponse, ISummaryWidgetService } from '../@intermediate/summarywidget';

@Injectable({
  providedIn: 'root'
})

export class SummaryWidgetService implements ISummaryWidgetService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/SummaryWidget';
  }

  public Get(request: GetSummaryWidgetRequest): Observable<GetSummaryWidgetResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetSummaryWidgetResponse>(apiUrl, { params: <any>request });
  }
}
