import { NgModule } from '@angular/core';
import { CustomPDFViewerComponent } from './custom-pdf-viewer.component';
import { PdfViewerModule } from 'ng2-pdf-viewer'; //https://www.npmjs.com/package/ng2-pdf-viewer;
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';

const BASE_MODULES = [];
const MATERIAL_MODULES = [PdfViewerModule, MatButtonModule];
