import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { GetItinerarySettingsRequest, GetItinerarySettingsResponse, ItinerarySettingsForm, PostItinerarySettingsRequest } from '../../@intermediate/itinerary-settings';
import { ItinerarySettingsService } from '../../@services/itinerary-settings.service';

@Component({
  selector: 'tei-my-account-itinerary-setting',
  templateUrl: './itinerary-setting.component.html',
  styleUrls: ['./itinerary-setting.component.css']
})
export class ItinerarySettingComponent implements OnInit{

  breadCrumbData: BreadCrumb[];
  model: GetItinerarySettingsResponse;
  form: FormGroup<ItinerarySettingsForm> = new FormGroup<ItinerarySettingsForm>({
    HideTeeTimeBookingNotes: new FormControl(null)
  });
  isLoading = false;

  constructor(private translate: TranslateService,
    private itinerarySettingsService: ItinerarySettingsService,
    private snackBar: SnackBarService)
  {
  }

  ngOnInit(): void
  {
    this.setBreadCrumbData();
    this.load();
  }

  setBreadCrumbData(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
        relativeUrl: '/office/contacts/setup'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
        relativeUrl: '/office/contacts/setup/web-account-configuration'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.MY_ITINERARY'),
        relativeUrl: "",
      },
    ];
  }

  async load(): Promise<void>
  {
    if (!this.model)
    {
      this.model = new GetItinerarySettingsResponse();
    }

    this.isLoading = true;
    try
    {
      this.isLoading = true;
      this.model = await firstValueFrom(this.itinerarySettingsService.Get(new GetItinerarySettingsRequest()));
    } catch (error)
    {
      this.snackBar.showError(error.error.title, '', null, 'left', 'bottom');
    }
    this.form.setValue(this.model);
    this.isLoading = false;
  }

  async save(): Promise<void>
  {
    try
    {
      this.isLoading = true;
      const request: PostItinerarySettingsRequest = {
        HideTeeTimeBookingNotes: Boolean(this.form.get('HideTeeTimeBookingNotes').value)
      };
      await firstValueFrom(this.itinerarySettingsService.Post(request));
      this.snackBar.showInfo(this.translate.instant('MY_ACCOUNT.ITINERARY_SETTINGS.SAVED_SETTINGS'), '', null, 'left', 'bottom');
    } catch (error)
    {
    }
    this.isLoading = false;
  }
}
