import { Component, OnInit, Inject, Pipe, PipeTransform } from '@angular/core';
import { IStatementPaymentService, GetStatementPaymentRequest, GetStatementPaymentResponse, PostStatementPaymentRequest, PostStatementPaymentRequestForm, PostStatementPaymentResponse, StatementPaymentDetail } from '../../@intermediate/statement-payment';
import { SnackBarService } from '@tei/material';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';

@Component({
  selector: 'tei-my-account-pages-statement-payment',
  templateUrl: './statement-payment.component.html',
  styleUrls: ['./statement-payment.component.scss']
})
export class StatementPaymentComponent implements OnInit
{
  model: GetStatementPaymentResponse;
  form: PostStatementPaymentRequestForm;
  currencySymbol: string;

  constructor(@Inject('IStatementPaymentService') private StatementPaymentService: IStatementPaymentService,
    private snackbarService: SnackBarService,
    private router: Router,
    private snackBar: SnackBarService,
    private translateService: TranslateService,
    private currencyService: TeiCurrencyTranslateService)
  {

  }

  ngOnInit()
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.form = new PostStatementPaymentRequestForm();
    const request = new GetStatementPaymentRequest();

    this.load(request);
  }

  private load(request: GetStatementPaymentRequest)
  {
    if (!this.model)
    {
      this.model = new GetStatementPaymentResponse();
    }

    this.StatementPaymentService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(form: PostStatementPaymentRequestForm)
  {
    //form.controls.StatementPayments.patchValue(this.model.StatementPayments);

    let request = new PostStatementPaymentRequest();
    const statements = form.value.StatementPayments.filter(f => f.IsBeingPaid == true);
    request.StatementPayments = statements;

    this.StatementPaymentService.Post(request).subscribe(response =>
    {
      if (response.Message != null && response.Message != "")
      {
        this.snackBar.showSuccess(this.translateService.instant('MY_ACCOUNT.STATEMENT_PAYMENT.INVALID_AMOUNT'), "", null, "left", "bottom");
      }
      else
      {
        if (response.SessionID && response.SessionID !== '')
        {
          localStorage.setItem("PaymentEngineReturnURL", window.location.href.replace('statement-payment', 'statements'));

          var custAccoutID = this.model.StatementPayments[0]?.CustAccountID;

          this.router.navigate([response.URL, response.SessionID, custAccoutID]);
        }
      }
    },
      error =>
      {
        //console.log(error["error"].Message);
        this.snackbarService.showError("No statements payments could be made due to an internal error.");
      });
  }

  get displayTotal()
  {
    if (this.model.StatementPayments)
    {
      return this.model.StatementPayments.filter(x => x.IsBeingPaid).reduce((sum, current) => sum + current.dAmountToPay, 0).toFixed(2);
    }
    else
    {
      return 0.00;
    }
  }

  onChangingAmountToPay(m: StatementPaymentDetail, event)
  {
    if (m.dAmountDue !== event)
    {

      if (this.model.PaymentAmountsPermitted === 'EXACTA')
      {
        event = m.dAmountDue;
        m.IsBeingPaid = false;
      }
      else if (this.model.PaymentAmountsPermitted === 'PARTIA' && (event > m.dAmountDue || event <= 0))
      {
        setTimeout(() =>
        {
          event = this.updateTotal(event, m);
        }, 200);

        this.snackbarService.showInfo('Please enter an amount equal to or lesser than $' + m.dAmountDue.toFixed(2));
      }
      else if (this.model.PaymentAmountsPermitted === 'OVERAM' && (event < m.dAmountDue || event <= 0))
      {
        setTimeout(() =>
        {
          event = this.updateTotal(event, m);
        }, 200);

        this.snackbarService.showInfo('Please enter an amount equal to or greater than $' + m.dAmountDue.toFixed(2));
      }
      else if (event <= 0)
      {
        m.IsBeingPaid = false;
        event = m.dAmountDue;
      }

    }

    m.dAmountToPay = event;

  }

  private updateTotal(event: any, m: StatementPaymentDetail)
  {
    event = m.dAmountDue;
    m.dAmountToPay = m.dAmountDue;
    return event;
  }
}

@Pipe({
  name: 'removeComma'
})
export class RemoveCommaPipe implements PipeTransform
{

  transform(value: string): string
  {
    if (value !== undefined && value !== null)
    {
      return value.replace(/,/g, "");
    } else
    {
      return "";
    }
  }
}
