export interface Validator
{
  name: string;
  validator: any;
  message: string;
}
export interface FieldConfig
{
  identifier?: string;
  label?: string;
  name?: string;
  name1?: string;
  name2?: string;
  formgroupname?: string;
  inputType?: string;
  options?: Array<FieldOption>;
  type: string;
  value?: any;
  value1?: any;
  value2?: any;
  validations?: Validator[];
  isMultiSelect?: boolean;
  isDateToggle?: boolean;
  needAll?: boolean;
  addedCSS?: string;
  isSearchText?: boolean;
  isReset?: boolean;
  dropdownOrientation?: string;
  pastDateValue?: number;

}
export interface FieldOption
{
  identifier?: string;
  display?: string;
  value?: string;
  icon?: string;
  expanded?: boolean;
  children?: FieldOption[];
}

export class GetItineraryRequest
{
  public StartDate: string;
  public EndDate?: string;
}

export class GetItineraryResponse
{
  public ItineraryGroups: Array<ItineraryItemGroup>;
  public FilterConditions: Array<FieldConfig>;
}

export class ItineraryItemGroup
{
  public Title: string;
  public Title1: string;
  public Subtitle: string;
  public ItineraryItems: Array<ItineraryItem>;
}

export class ItineraryItem
{
  public Title: string;
  public Subtitle: string;
  public ImageUrl: string;
  public Notes: string;
  public DesiredCourses: string;
  public MemberDetails: Array<MemberDetail>;
  public IsManage: boolean;
  public IsCheckIn: boolean;
  public ManageURL: string;
  public ReferenceId: string;
  public NotificationType: string;
  public CanManageMessage: string;
  public Type: string;
  public ReservationID: string;
  public ReservationStatus: number;
  public IsReservationCancelled: boolean;
  public CanCancel: boolean;
  public IsDisplayTime: boolean;
  public BookingDateTime: string;
  public CourseName: string;
  public PartySize: number;
  public RestaurantName: string;
  public ServiceName: string;
  public EventName: string;
  public EventPackageName: string;
  public DisplayTime: string;
  public ReservationStatusResponse?: GetReservationStatusResponse;
  PaidStatus: boolean;
}

export class MemberDetail
{
  public Title: string;
  public Subtitle: string;
  public ImageUrl: string;
}

