export class GetReservationStatusRequest
{
  ReservationID: string;
}

export class GetReservationStatusResponse
{
  ReservationStatus: number;
  IsReservationCancelled: boolean;
  IsAnythingChanged: boolean;
  ReservationChanges: ReservationChangesResult;
}

export class ReservationChangesResult
{
  IsPartySizeChanged: boolean;
  IsReservationDateTimeChanged: boolean;
  NewPartySize: number;
  NewReservationDateTime: Date;
}
