import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMyProfileFieldRequest, GetMyProfileFieldResponse, IMyProfileFieldService, PostMyProfileFieldRequest, PostMyProfileFieldResponse, SortOrderMyProfileFieldRequest, SortOrderMyProfileFieldResponse } from '../@intermediate/myprofile-field';


@Injectable({
  providedIn: 'root'
})
export class MyProfileFieldService implements IMyProfileFieldService
{

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/MyProfileField';
  }

  Get(request: GetMyProfileFieldRequest): Observable<GetMyProfileFieldResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMyProfileFieldResponse>(apiUrl, request);
  }

  SortOrder(request: SortOrderMyProfileFieldRequest): Observable<SortOrderMyProfileFieldResponse>
  {
    const apiUrl = this.serviceUrl + '/SortOrder';
    return this.http.post<SortOrderMyProfileFieldResponse>(apiUrl, request);
  }

  Post(request: PostMyProfileFieldRequest): Observable<PostMyProfileFieldResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMyProfileFieldRequest>(apiUrl, request);
  }
}
