import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { StatementPaymentSetting } from '../../statement-payment-setting';
import { StatementTableSetting } from '../../statements-table-settings/models/post-statement-table-setting.model';
import { GetMyStatementsSettingsResponse, StatementsSettings } from './get-my-statements-settings.model';
import { StatementPropertySetting } from '../../statement-property-settings/models/post-statement-property-settings.model';

export class PostMyStatementsSettingsRequest
{
  public DisplayType: string;
  public BalanceType: string;
  public ShowAccBalanceTo: string;
  public StatementPaymentSettings: StatementPaymentSetting | "";
  public StatementTableSettings: StatementTableSetting | "";
}

export class PostMyStatementsSettingsRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      DisplayType: new UntypedFormControl('STONLY'),
      BalanceType: new UntypedFormControl('ACCBAL'),
      ShowAccBalanceTo: new UntypedFormControl('LASINV'),
      StatementPaymentSettings: new UntypedFormControl(''),
      StatementTableSettings: new UntypedFormControl('')
    });
  }

  public value: PostMyStatementsSettingsRequest;

  public setValue(value: GetMyStatementsSettingsResponse)
  {
    const request = new PostMyStatementsSettingsRequest();
    request.BalanceType = value.Settings[0].BalanceType;
    request.DisplayType = value.Settings[0].DisplayType;
    request.ShowAccBalanceTo = value.Settings[0].ShowAccountBalanceTo;
    request.StatementPaymentSettings = setPaymentSettings(value.Settings[0].StatementPaymentSettings, request);
    request.StatementTableSettings = value.Settings[0].StatementTableSettings;
    super.setValue(request);

  }
}

export class PostMyStatementsSettingsResponse
{
  public IsSuccess: boolean;
}

function setPaymentSettings(value: StatementPaymentSetting, request: PostMyStatementsSettingsRequest): StatementPaymentSetting
{
  request.StatementPaymentSettings = new StatementPaymentSetting();

  request.StatementPaymentSettings.EnableStatementPayments = value.EnableStatementPayments;
  request.StatementPaymentSettings.ApplyPaymentsTo = value.ApplyPaymentsTo;
  request.StatementPaymentSettings.DisplayZeroOrFullyPaidInvoices = value.DisplayZeroOrFullyPaidInvoices;
  request.StatementPaymentSettings.EnableApplyPaymentByDefault = value.EnableApplyPaymentByDefault;
  request.StatementPaymentSettings.PermittedPaymentAmount = value.PermittedPaymentAmount;
  request.StatementPaymentSettings.EnableCreditCardPayments = value.EnableCreditCardPayments;
  request.StatementPaymentSettings.EnableECheckOrACHPayments = value.EnableECheckOrACHPayments;
  request.StatementPaymentSettings.EnablePreAuthorizedPayments = value.EnablePreAuthorizedPayments;
  request.StatementPaymentSettings.PreAuthorizedCreditCardMessage = value.PreAuthorizedCreditCardMessage;
  request.StatementPaymentSettings.PreAuthorizedECheckMessage = value.PreAuthorizedECheckMessage;

  return request.StatementPaymentSettings;
}


