import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GetAddressRequest, GetAddressResponse, IAddressService } from '../../@intermediate/address';

@Component({
  selector: 'tei-my-account-pages-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.scss']
})
export class AddressComponent implements OnInit
{
  model: GetAddressResponse;
  address: string;
  changeAddressID: string;
  navigatedFromDynamicRouter: boolean;

  constructor(@Inject('IAddressService') private AddressService: IAddressService,
    private route: ActivatedRoute,
    private router: Router)
  {
    this.navigatedFromDynamicRouter = this.route.snapshot?.data["NavigatedFromDynamicRouter"] ?? false;
    this.route.params.subscribe(params =>
    {
      this.address = params.id;
    });
  }

  ngOnInit()
  {
    const request = new GetAddressRequest();
    this.load(request);
  }

  private load(request: GetAddressRequest)
  {
    if (!this.model)
    {
      this.model = new GetAddressResponse();
    }

    this.AddressService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  getAddress(event: any)
  {
    if (this.navigatedFromDynamicRouter)
    {
      if (this.address)
      {
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else
      {
        this.router.navigate(['../'], { relativeTo: this.route });
      }
    } else
    {
      if (event)
      {
        this.router.navigate(['../../addresses'], { relativeTo: this.route });
      } else
      {
        this.router.navigate(['../../../addresses'], { relativeTo: this.route });
      }
    }

  }
}
