import { NoopScrollStrategy } from '@angular/cdk/overlay';
import
{
  Component,
  ComponentFactoryResolver, Inject, Input, OnInit,
  Pipe,
  PipeTransform,
  SecurityContext,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { AcquireDiningReservationDetailSelected, IDiningState } from '@tei/booking-dining-states';
// import { BottomToolbarComponent } from '@tei/common-toolbar';
import { SnackBarService } from '@tei/material';
import { DynamicFormService } from '@tei/material-generic-filter';
import moment from 'moment';
import { switchMap } from "rxjs/operators";
import
{
  GetItineraryResponse,
  GetReservationStatusRequest,
  IItineraryService,
  ItineraryItem,
  PostCancelActivityRequest,
  PostCancelActivityResponse,
  PostCancelEventRequest,
  PostCancelEventResponse,
  PostCancelLotteryRequest,
  PostCancelLotteryResponse,
  PostCancelReservationRequest,
  PostCancelReservationResponse
} from '../../@intermediate/itinerary';
import { GetItineraryFilterResponse } from '../../@intermediate/itinerary-filter/models/get-itinerary-filter.model';
import { ItineraryConfirmationDialogComponent, ItineraryConfirmationDialogResult, ItineraryConfirmationDialogSettings } from '../../components/itinerary-confirmation-dialog/itinerary-confirmation-dialog.component';

@Component({
  selector: 'tei-my-account-itinerary-item',
  templateUrl: './itinerary-item.component.html',
  styleUrls: ['./itinerary-item.component.css']
})
export class ItineraryItemComponent implements OnInit
{
  @Input('Item') item: ItineraryItem;
  @ViewChild('placeHolder', { read: ViewContainerRef }) private _placeHolder: ViewContainerRef;
  model: GetItineraryResponse;
  filtermodel: GetItineraryFilterResponse;
  // bottomToolBar: BottomToolbarComponent;
  pageLength: number;
  filterTodayDate: moment.Moment = moment();
  formValues: any[] = [];
  showFilter: boolean = false;

  DINING_RESERVATION_STATUS_CANCELLED = 3;
  constructor(@Inject('IItineraryService') private ItineraryService: IItineraryService,
    private dynamicfilter: DynamicFormService,
    private snackBar: SnackBarService,
    public dialog: MatDialog,
    private teiTextTranslateService: TranslateService,
    private _cmpFctryRslvr: ComponentFactoryResolver,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private store: Store<IDiningState>,
    public translateService: TranslateService,
    private sanitizer: DomSanitizer) { }

  ngOnInit(): void
  {
  }

  sanitizeHtml(html: string): SafeHtml
  {
    // Angular will sanitize the input and remove unsafe content
    return this.sanitizer.sanitize(SecurityContext.HTML, html) || '';
  }


  onItineraryItemOpened(itineraryItem: ItineraryItem): void
  {
    if (!itineraryItem)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    if (itineraryItem.Type !== 'OpenTable')
    {
      //Nothing
      return;
    }

    if (itineraryItem.IsReservationCancelled)
    {
      //Nothing
      return;
    }

    if (!itineraryItem.ReservationID)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    var request = { ReservationID: itineraryItem.ReservationID } as GetReservationStatusRequest;

    this.ItineraryService.GetDiningReservationStatus(request).subscribe(
      response =>
      {
        itineraryItem.IsReservationCancelled = response.IsReservationCancelled;
        itineraryItem.ReservationStatusResponse = response;
      },
      error =>
      {
        this.snackBar.showInfo(error.error.title, '', null, 'center', 'bottom');
        return;
      }
    );
  }

  isContactHoldTbd(contactId: string): boolean
  {

    if (contactId == '88888888-8888-8888-8888-888888888888')
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  diningCancelReservation(itineraryItem: ItineraryItem): void
  {
    if (!itineraryItem)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    if (itineraryItem.Type !== 'OpenTable')
    {
      //this should not happen. so if we have this situation something is wrong
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      //Nothing
      return;
    }

    if (!itineraryItem.ReservationID)
    {
      this.snackBar.showInfo("Error on action", '', null, 'center', 'bottom');
      return;
    }

    let dialogRef = this.dialog.open(ItineraryConfirmationDialogComponent, {
      panelClass: '',
      height: '',
      maxWidth: '300px',
      data: {
        settings: {
          title: this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.CANCEL_CONFIRM_DIALOG_TITLE'),
          message: this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.CANCEL_CONFIRM_DIALOG_MESSAGE'),
        } as ItineraryConfirmationDialogSettings
      },
      scrollStrategy: new NoopScrollStrategy()
    });

    dialogRef.afterClosed().pipe(
      switchMap(result =>
      {
        if (!result)
        {
          //don't need any action
          return;
        }

        var res = result as ItineraryConfirmationDialogResult;

        if (!res || !res.isConfirmed)
        {
          //don't need any action
          return;
        }

        var request =
          {
            ReservationID: itineraryItem.ReservationID
          } as PostCancelReservationRequest;

        return this.ItineraryService.CancelDiningReservation(request);
      })).subscribe(
        response =>
        {
          this.handleDiningReservationCancelResult(itineraryItem, response);
        },
        error =>
        {
          this.snackBar.showInfo(error.error.title, '', null, 'center', 'bottom');
        });
  }


  lotteryCancelRequest(itineraryItem: ItineraryItem): void
  {
    let dialogRef = this.dialog.open(ItineraryConfirmationDialogComponent, {
      panelClass: '',
      height: '',
      maxWidth: '300px',
      data: {
        settings: {
          title: this.teiTextTranslateService.instant('MY_ACCOUNT.LOTTERY.CANCEL_CONFIRM_DIALOG_TITLE'),
          message: this.teiTextTranslateService.instant('MY_ACCOUNT.LOTTERY.CANCEL_CONFIRM_DIALOG_MESSAGE'),
        } as ItineraryConfirmationDialogSettings
      },
      scrollStrategy: new NoopScrollStrategy()
    });

    dialogRef.afterClosed().pipe(
      switchMap(result =>
      {
        if (!result)
        {
          //don't need any action
          return;
        }

        var res = result as ItineraryConfirmationDialogResult;

        if (!res || !res.isConfirmed)
        {
          //don't need any action
          return;
        }

        var request =
          {
            LotteryID: itineraryItem.ReferenceId
          } as PostCancelLotteryRequest;

        return this.ItineraryService.CancelLotteryRequest(request);
      })).subscribe(
        response =>
        {
          this.handleLotteryCancelResult(itineraryItem, response);
        },
        error =>
        {
          this.snackBar.showInfo(error.error.title, '', null, 'center', 'bottom');
        });
  }

  handleLotteryCancelResult(itineraryItem: ItineraryItem, response: PostCancelLotteryResponse): void
  {
    if (response.IsSuccess)
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.LOTTERY.RESERVATION_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');

      itineraryItem.IsReservationCancelled = true;
    }
    else
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.LOTTERY.RESERVATION_NOT_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');
    }
  }


  handleDiningReservationCancelResult(itineraryItem: ItineraryItem, response: PostCancelReservationResponse): void
  {
    if (response.IsSuccess)
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.RESERVATION_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');

      itineraryItem.IsReservationCancelled = true;
    }
    else
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.RESERVATION_NOT_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');
    }
  }



  activityCancelBooking(itineraryItem: ItineraryItem): void
  {
    let dialogRef = this.dialog.open(ItineraryConfirmationDialogComponent, {
      panelClass: '',
      height: '',
      maxWidth: '300px',
      data: {
        settings: {
          title: this.teiTextTranslateService.instant('MY_ACCOUNT.ACTIVITY.CANCEL_CONFIRM_DIALOG_TITLE'),
          message: this.teiTextTranslateService.instant('MY_ACCOUNT.ACTIVITY.CANCEL_CONFIRM_DIALOG_MESSAGE'),
          confirmBtnText: this.teiTextTranslateService.instant('MY_ACCOUNT.ACTIVITY.CANCEL'),

        } as ItineraryConfirmationDialogSettings
      },
      scrollStrategy: new NoopScrollStrategy()
    });

    dialogRef.afterClosed().pipe(
      switchMap(result =>
      {
        if (!result)
        {
          //don't need any action
          return;
        }

        var res = result as ItineraryConfirmationDialogResult;

        if (!res || !res.isConfirmed)
        {
          //don't need any action
          return;
        }

        const request: PostCancelActivityRequest = {
          ReferenceID: itineraryItem.ReferenceId,
          AppointmentID: itineraryItem.ReservationID
        };

        return this.ItineraryService.CancelActivityRequest(request);
      })).subscribe(
        response =>
        {
          this.handleActivityCancelResult(itineraryItem, response);
        },
        error =>
        {
          this.snackBar.showInfo(error.error.Title, '', null, 'center', 'bottom');
        });
  }

  handleActivityCancelResult(itineraryItem: ItineraryItem, response: PostCancelActivityResponse): void
  {
    if (response.Message == "Appointment cancelled")
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.ACTIVITY.RESERVATION_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');

      itineraryItem.IsReservationCancelled = true;
    }
    else
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.ACTIVITY.RESERVATION_NOT_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');
    }
  }


  eventCancelBooking(itineraryItem: ItineraryItem): void
  {
    let dialogRef = this.dialog.open(ItineraryConfirmationDialogComponent, {
      panelClass: '',
      height: '',
      maxWidth: '300px',
      data: {
        settings: {
          title: this.teiTextTranslateService.instant('MY_ACCOUNT.EVENT.CANCEL_CONFIRM_DIALOG_TITLE'),
          message: this.teiTextTranslateService.instant('MY_ACCOUNT.EVENT.CANCEL_CONFIRM_DIALOG_MESSAGE'),
        } as ItineraryConfirmationDialogSettings
      },
      scrollStrategy: new NoopScrollStrategy()
    });

    dialogRef.afterClosed().pipe(
      switchMap(result =>
      {
        if (!result)
        {
          //don't need any action
          return;
        }

        var res = result as ItineraryConfirmationDialogResult;

        if (!res || !res.isConfirmed)
        {
          //don't need any action
          return;
        }

        var request =
          {
            ReferenceID: itineraryItem.ReferenceId
          } as PostCancelEventRequest;

        return this.ItineraryService.CancelEventRequest(request);
      })).subscribe(
        response =>
        {
          this.handleEventCancelResult(itineraryItem, response);
        },
        error =>
        {
          this.snackBar.showInfo(error.error.title, '', null, 'center', 'bottom');
        });
  }

  handleEventCancelResult(itineraryItem: ItineraryItem, response: PostCancelEventResponse): void
  {
    if (response.IsSuccess)
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.EVENT.RESERVATION_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');

      itineraryItem.IsReservationCancelled = true;
    }
    else
    {
      this.snackBar.showInfo(this.teiTextTranslateService.instant('MY_ACCOUNT.EVENT.RESERVATION_NOT_CANCELLED_MESSAGE'), '', null, 'center', 'bottom');
    }
  }

  manageDining(itineraryItem)
  {
    this.dispatchAcquireDiningReservationAction(new AcquireDiningReservationDetailSelected({
      ReservationID: itineraryItem.ReservationID
    }));

    this.router.navigate([itineraryItem.ManageURL]);
  }

  manageTeeTimes(itineraryItem)
  {
    this.router.navigate(
      ['/web/tee-times/teetime-details', itineraryItem.ReferenceId, true],
      { replaceUrl: true }
    );
  }

  manageTeeTimeRequest(itineraryItem)
  {
    const date = new Date(itineraryItem.BookingDateTime);
    const dd = date.getDate();
    const mm = date.getMonth() + 1;
    const yyyy = date.getFullYear();

    this.router.navigate(
      ['/web/lottery/lottery-request', dd, mm, yyyy, true],
      { replaceUrl: true }
    );
  }


  private dispatchAcquireDiningReservationAction(AcquireDiningReservationDetailSelected: AcquireDiningReservationDetailSelected)
  {
    this.store.dispatch(AcquireDiningReservationDetailSelected);
  }

  notification(i: any)
  {
    // //this.bottomToolBar = window["angularRefresh"].component;
    // setTimeout(() =>
    // {
    //   let v =
    //   {
    //     NotificationType: i.NotificationType,
    //     ReferenceId: i.ReferenceId
    //   };
    //   this.bottomToolBar.openBottomSheet(v);
    // }, 300);
  }

  manageActivity(itineraryItem: ItineraryItem): void
  {
    this.router.navigate(['../../activities/reservation-details', itineraryItem.ReservationID], { relativeTo: this.activatedRoute });
  }

  manageEvent(itineraryItem: ItineraryItem): void
  {
    this.router.navigate(['../../event/reservation-details', itineraryItem.ReservationID], { relativeTo: this.activatedRoute });
  }
}

@Pipe({ name: 'safeHtml' })
export class Safe implements PipeTransform
{
  constructor(private sanitizer: DomSanitizer) { }

  transform(style: string)
  {
    style = style.replace(/&lt;/g, '<').replace(/&gt;/g, '>');
    return this.sanitizer.bypassSecurityTrustHtml(style);
    //return this.sanitizer.bypassSecurityTrustStyle(style);
    // return this.sanitizer.bypassSecurityTrustXxx(style); - see docs
  }
}
