import { Inject, Injectable } from "@angular/core";
import { IItinerarySettingsService } from "../@intermediate/itinerary-settings/itinerary-settings.interface";
import { IAPIBaseUrlProvider } from "@tei/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { GetItinerarySettingsRequest, GetItinerarySettingsResponse } from "../@intermediate/itinerary-settings/models/get-itinerary-settings.model";
import { PostItinerarySettingsRequest, PostItinerarySettingsResponse } from "../@intermediate/itinerary-settings/models/post-itinerary-settings.model";

@Injectable({
  providedIn: 'root'
})

export class ItinerarySettingsService implements IItinerarySettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Settings';
  }

  Get(request: GetItinerarySettingsRequest): Observable<GetItinerarySettingsResponse> {
    const apiUrl = this.serviceUrl + '/GetMyItinerarySetting';
        return this.http.get<GetItinerarySettingsResponse>(apiUrl);
  }

  Post(request: PostItinerarySettingsRequest): Observable<PostItinerarySettingsResponse> {
      const apiUrl = this.serviceUrl + '/PostMyItinerarySetting';
        return this.http.post<PostItinerarySettingsResponse>(apiUrl, request);
  }
}
