import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetStatementPaymentRequest, GetStatementPaymentResponse, IStatementPaymentService, PostStatementPaymentRequest, PostStatementPaymentResponse } from '../@intermediate/statement-payment';

@Injectable({
  providedIn: 'root'
})

export class StatementPaymentService implements IStatementPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/StatementPayment';
  }

  public Get(request: GetStatementPaymentRequest): Observable<GetStatementPaymentResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetStatementPaymentResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostStatementPaymentRequest): Observable<PostStatementPaymentResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostStatementPaymentResponse>(apiUrl, request);
  }
}
