import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SelectedAddress } from '@tei/common-my-account-shared';
import { CreditCardDetail } from '@tei/common-payment-engine-shared';
import { Subscription } from 'rxjs';
import { IPaymentMethodService, PostPaymentMethodRequest, PostPaymentMethodRequestForm } from '../../@intermediate/paymentmethod';

@Component({
  selector: 'tei-my-account-pages-paymentmethod',
  templateUrl: './paymentmethod.component.html',
  styleUrls: ['./paymentmethod.component.scss']
})
export class PaymentMethodComponent implements OnInit, OnDestroy
{
  form: PostPaymentMethodRequestForm;
  subscription = new Subscription();
  navigatedFromDynamicRouter: boolean;
  paymentMethodId: string;

  constructor(@Inject('IPaymentMethodService') private PaymentMethodService: IPaymentMethodService,
    private route: ActivatedRoute,
    private router: Router)
  {
    this.navigatedFromDynamicRouter = this.route.snapshot?.data["NavigatedFromDynamicRouter"] ?? false;
  }

  ngOnInit()
  {
    this.form = new PostPaymentMethodRequestForm();

    this.subscription.add(this.route.params.subscribe(params =>
    {
      this.paymentMethodId = params.id;
      this.form.controls.creditCard.patchValue(params.id);
    }));
  }

  save()
  {
    const request = new PostPaymentMethodRequest();
    request.address = this.form.value.selectedAddress.SelectedAddressDetail ? this.form.value.selectedAddress.SelectedAddressDetail.AddressID : "";
    request.creditCard = this.form.value.creditCard;
    request.isDeleted = false;
    request.isDisplayed = true;
    this.PaymentMethodService.Post(request).subscribe(response =>
    {
      this.backToPaymentMethods();
    });
  }

  getCreditCardDetails(event: CreditCardDetail)
  {
    this.form.controls.creditCard.patchValue(event.CreditCardID);
    if (event?.BillingAddress?.AddressID)
    {
      let addr = new SelectedAddress();
      addr.SelectedAddressDetail = event.BillingAddress;
      this.form.controls.selectedAddress.patchValue(addr);
    }
  }

  backToPaymentMethods()
  {
    if (!this.navigatedFromDynamicRouter)
    {
      this.router.navigate(["/web/my-account/paymentmethods"], { relativeTo: this.route.parent });
    } else
    {
      if (this.paymentMethodId)
      {
        this.router.navigate(["../../"], { relativeTo: this.route });
      } else
      {
        this.router.navigate(["../"], { relativeTo: this.route });
      }
    }
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
