export class GetSummaryWidgetRequest
{
  public RewardsLabel: string;
  public ContactID: string = "";
}

export class GetSummaryWidgetResponse
{
  public summaryWidgetResponse: Array<SummaryWidgetResponse>;
  public Settings: SummaryWidgetGlobalSettings;
}

export class SummaryWidgetResponse
{
  public DisplayPercentage: number;
  public DisplayValue: string;
  public Icon: string;
  public SubDisplayValue: string;
  public SubTitle: string;
  public Title: string;
  public WidgetName: string;
  public WidgetType: number;
  public Width: number;
}

export class SummaryWidgetGlobalSettings
{
  public EnableFBMinimums: boolean;
  public EnableRewards: boolean;
  public EnableCashEQ: boolean;
}
