import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { SelectAddressModule } from '@tei/common-my-account-shared';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { AddressesService } from '../../@services/addresses.service';
import { AddressesComponent } from './addresses.component';


@NgModule({
  declarations: [AddressesComponent],
  imports: [
    RouterModule.forChild([
      { path: '', component: AddressesComponent },
      { path: 'add', loadChildren: () => import("../address/address.module").then(m => m.AddressModule), data: { NavigatedFromDynamicRouter: true } },
      { path: 'edit', loadChildren: () => import("../address/address.module").then(m => m.AddressModule), data: { NavigatedFromDynamicRouter: true } },
    ]),
    CommonModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    FormsModule,
    ReactiveFormsModule,
    SnackBarModule,
    ConfirmDialogModule,
    SelectAddressModule,
    TeiTextTranslatePipeModule,
    TranslateModule

  ],
  providers: [{ provide: 'IAddressesService', useClass: AddressesService }],
  //bootstrap: [AddressesComponent]
})
export class AddressesModule { }
