import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import 
{
  GetItineraryRequest,
  GetItineraryResponse,
  GetReservationStatusRequest,
  GetReservationStatusResponse,
  IItineraryService,
  PostCancelActivityRequest,
  PostCancelActivityResponse,
  PostCancelEventRequest,
  PostCancelEventResponse,
  PostCancelLotteryRequest,
  PostCancelLotteryResponse,
  PostCancelReservationRequest,
  PostCancelReservationResponse,
  PostItineraryRequest,
  PostItineraryResponse
} from '../@intermediate/itinerary';
import { GetItineraryFilterRequest, GetItineraryFilterResponse } from '../@intermediate/itinerary-filter/models/get-itinerary-filter.model';

@Injectable({
  providedIn: 'root'
})

export class ItineraryService implements IItineraryService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + 'Itinerary';
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Itinerary';
  }

  public Get(request: GetItineraryRequest): Observable<GetItineraryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetItineraryResponse>(apiUrl, { params: <any>request });
  }

  public GetItineraryFilter(request: GetItineraryFilterRequest): Observable<GetItineraryFilterResponse>
  {
    const apiUrl = this.serviceUrl + '/GetItineraryFilter';
    return this.http.get<GetItineraryFilterResponse>(apiUrl, { params: <any>request });
  }

  public Post(request: PostItineraryRequest): Observable<PostItineraryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostItineraryResponse>(apiUrl, request);
  }



  GetDiningReservationStatus(request: GetReservationStatusRequest): Observable<GetReservationStatusResponse>
  {
    const apiUrl = this.serviceUrl + '/GetDiningReservationStatus';
    return this.http.get<GetReservationStatusResponse>(apiUrl, { params: request as any });
  }
  CancelDiningReservation(request: PostCancelReservationRequest): Observable<PostCancelReservationResponse>
  {
    const apiUrl = this.serviceUrl + '/PostDiningReservationCancel';
    return this.http.post<PostCancelReservationResponse>(apiUrl, request);
  }

  CancelLotteryRequest(request: PostCancelLotteryRequest): Observable<PostCancelLotteryResponse>
  {
    const apiUrl = this.serviceUrl + '/PostCancelLotteryRequest';
    return this.http.post<PostCancelLotteryResponse>(apiUrl, request);
  }

  CancelActivityRequest(request: PostCancelActivityRequest): Observable<PostCancelActivityResponse>
  {
    const apiUrl = this.serviceUrl + '/PostCancelActivityRequest';
    return this.http.post<PostCancelActivityResponse>(apiUrl, request);
  }

  CancelEventRequest(request: PostCancelEventRequest): Observable<PostCancelEventResponse>
  {
    const apiUrl = this.serviceUrl + '/PostCancelEventRequest';
    return this.http.post<PostCancelEventResponse>(apiUrl, request);
  }

}
