export class GetPaymentMethodsRequest { }

export class GetPaymentMethodsResponse
{
  public AllowCreditCards: boolean;
  public AllowEChecks: boolean;
  public AllowRedemptions: boolean;
  public EnablePaymentMethodOnAccount: boolean;
  public CashEQsAvailableForRedemption: CashEQsAvailableForRedemption[];
  public RedeemedCashEQs: CashEQsAvailableForRedemption[];
  public ARAccounts: ARAccount[];
}

export class GetVoucherDetailsRequest
{
  public CertificateID: string;
}

export class GetVoucherDetailsResponse
{
  public VoucherDetailInfo: VoucherDetail;
  public CourseInfo: GolfCourses[];
  public TransactionDetailsInfo: TransactionDetails[];
}

export class VoucherDetail
{
  public Name: string;
  public Description: string;
  public CashEqID: string;
  public CertID: string;
  public IssuedDate: Date;
  public ExpiryDate: Date;
  public OriginalBalance: number;
  public CurrentBalance: number;
  public IsPromo: boolean;
  public CertNumRounds: number;
  public MinPlayersRequired: number;
  public numPlayers: number;
  public CertType: number;
  public RedeemableAt: number;
  public RemainingRound: number;
  public CertificateID: string;
  public CourseNames: GolfCourses[];
  public Transactions: TransactionDetails[];
}

export class GolfCourses
{
  public CourseName: string;
}

export class TransactionDetails
{
  public TrasactionID: number;
  public TrxDate: Date;
  public TrxType: string;
  public Amount: number;
  public CompanyName: string;
  public POSID: string;
}

export class CashEQsAvailableForRedemption
{
  public Name: string;
  public Description: string;
  public Amount: number;
  public CashEqID: number;
  public CashEqItemUID: string;
  public Type: number;
  public RedeemOnline: number;
  public Redeemed: boolean;
  public AvailableRounds: number;
}

export interface ARAccount {
  AccountID: string;
  Name: string;
  AllowAR: boolean;
  CreditLimitAmount: number;
  CreditLimitType: number;
  MemberID: string;
  IsDefaultAccount: boolean;
  AccountBalance: number;
}
