import { FormControl, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { SelectedAddress } from '@tei/common-my-account-shared';
import { GetPaymentMethodResponse } from './get-paymentmethod.model';

export class PostPaymentMethodRequest
{
  public creditCard: string;
  public address: string;
  public isDisplayed: boolean;
  public isDeleted: boolean;
  public selectedAddress: SelectedAddress;
}

export class PostPaymentMethodRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      selectedAddress: new UntypedFormControl(''),
      creditCard: new FormControl('', Validators.required),
    });
  }

  public value: PostPaymentMethodRequest;

  public setValue(value: GetPaymentMethodResponse)
  {
    const request = new PostPaymentMethodRequest();

    request.selectedAddress = value.selectedAddress;

    super.setValue(request);
  }
}

export class PostPaymentMethodResponse
{

}
