import { Component, Inject, OnInit } from "@angular/core";
import { FormlyFieldConfig } from "@ngx-formly/core";
import { TranslateService } from "@ngx-translate/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import
  {
    GetTransactionSettingsRequest,
    GetTransactionSettingsResponse,
  } from "../../@intermediate/transaction-settings/models/get-transaction-setting.model";
import
  {
    PostTransactionSettingsRequest,
    PostTransactionSettingsRequestForm,
  } from "../../@intermediate/transaction-settings/models/post-transaction-setting.model";
import { ITransactionSettingsService } from "../../@intermediate/transaction-settings/transaction-setting.interface";

@Component({
  selector: "tei-my-account-pages-transaction-settings",
  templateUrl: "./transaction-settings.component.html",
  styleUrls: ["./transaction-settings.component.scss"],
})
export class TransactionSettingsComponent implements OnInit
{
  form: PostTransactionSettingsRequestForm;
  model: GetTransactionSettingsResponse;
  breadCrumbData: BreadCrumb[];
  isLoading = false;
  fields: FormlyFieldConfig[] = [
    {
      wrappers: ["formly-cutom-setting-card-wrapper"],
      templateOptions: {},
      fieldGroup: [
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "General Settings",
            description: `Configure display options for My Transactions.`,
          },
          fieldGroup: [
            {
              key: "EnableFBMinimums",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: "Enable F&B Minimums display",
              },
            },
            {
              key: "Pagination",
              type: "formly-custom-setting-select",
              templateOptions: {
                inputLabel: "Pagination",
                options: [
                  { value: 25, label: 25 },
                  { value: 50, label: 50 },
                  { value: 75, label: 75 },
                  { value: 100, label: 100 },
                ],
              },
            },
          ],
        },
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "Discount column",
            description: `Display the amount discounted in a column.`,
          },
          fieldGroup: [
            {
              key: "EnableDiscountColumn",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: "Enable Discount Column",
              },
            },
            {
              key: "DiscountLabel",
              type: "formly-custom-setting-label",
              templateOptions: {
                inputLabel: "Column header label",
                readonly: false,
              },
              expressionProperties: {
                "templateOptions.readonly": (model) => !model.EnableDiscountColumn,
              },
            },
          ],
        },
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "Display Additional Tip/Change ",
            description: `Display the change/additional tip amount for applicable F&B transactions.`,
          },
          fieldGroup: [
            {
              key: "EnableGratuityTips",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: "Enable Additional Tip/Change row for F&B transactions",
              },
            },
            {
              key: "GratuityTipslabel",
              type: "formly-custom-setting-label",
              templateOptions: {
                inputLabel: "Label",
                readonly: false,
              },
              expressionProperties: {
                "templateOptions.readonly": (model) =>
                  !model.EnableGratuityTips,
              },
            },
          ],
        },
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "Display Rewards",
            description: `Display a column indicating rewards earned/redeemed and the ability to filter by transaction containing rewards.`,
          },
          fieldGroup: [
            {
              key: "EnableRewards",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: "Enable Rewards",
              },
            },
            {
              key: "RewardsLabel",
              type: "formly-custom-setting-label",
              templateOptions: {
                inputLabel: "Label",
                readonly: false,
              },
              expressionProperties: {
                "templateOptions.readonly": (model) => !model.EnableRewards,
              },
            },
          ],
        },
        {
          wrappers: ["formly-custom-setting-row-wrapper"],
          templateOptions: {
            label: "Cash EQ",
            description: `Display a column indicating Cash EQ transactions and the ability to filter by transactions containing Cash EQs.`,
          },
          fieldGroup: [
            {
              key: "EnableCashEQ",
              type: "formly-custom-setting-check-box",
              templateOptions: {
                label: "Enable Cash EQ",
              },
            },
            {
              key: "CashEQLabel",
              type: "formly-custom-setting-label",
              templateOptions: {
                inputLabel: "Label",
                readonly: false,
              },
              expressionProperties: {
                "templateOptions.readonly": (model) => !model.EnableCashEQ,
              },
            },
          ],
        },
      ],
    },
  ];

  constructor(
    @Inject("ITransactionSettingsService")
    private myTransactionSettingsService: ITransactionSettingsService,
    private translate: TranslateService,
    private snackBar: SnackBarService
  ) { }

  ngOnInit()
  {
    this.form = new PostTransactionSettingsRequestForm();
    this.model = new GetTransactionSettingsResponse();
    this.setBreadCrumbData();
    this.load();
  }

  setBreadCrumbData()
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
        relativeUrl: '/office/contacts/setup'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
        relativeUrl: '/office/contacts/setup/web-account-configuration'
      },
      {
        title: this.translate.instant('MY_ACCOUNT.MY_TRANSACTIONS.SETTINGS'),
        relativeUrl: "",
      },
    ];
  }

  load()
  {
    this.isLoading = true;
    const request = new GetTransactionSettingsRequest();

    this.myTransactionSettingsService.Get(request).subscribe(
      (response) =>
      {
        if (!response)
        {
          response = {
            EnableFBMinimums: false,
            Pagination: 25,
            EnableDiscountColumn: true,
            DiscountLabel: "Discount",
            EnableGratuityTips: true,
            GratuityTipslabel: "Gratuity",
            EnableRewards: false,
            RewardsLabel: "Rewards",
            EnableCashEQ: false,
            CashEQLabel: "Cash EQ",
          };
        }

        this.model.EnableFBMinimums = response.EnableFBMinimums;
        this.model.Pagination = response.Pagination;
        this.model.EnableDiscountColumn = response.EnableDiscountColumn;
        this.model.DiscountLabel = response.DiscountLabel;
        this.model.EnableGratuityTips = response.EnableGratuityTips;
        this.model.GratuityTipslabel = response.GratuityTipslabel;
        this.model.EnableRewards = response.EnableRewards;
        this.model.RewardsLabel = response.RewardsLabel;
        this.model.EnableCashEQ = response.EnableCashEQ;
        this.model.CashEQLabel = response.CashEQLabel;
        if (this.model)
        {
          this.form.setValue(response);
        }
        this.isLoading = false;
      },
      (error) =>
      {
        this.snackBar.showInfo(error.error.title, "", null, "center", "bottom");
        this.isLoading = false;
      }
    );
  }

  save()
  {
    this.isLoading = true;
    let request = new PostTransactionSettingsRequest();
    request = this.form.value;
    this.myTransactionSettingsService.Post(request).subscribe(
      (response) =>
      {
        if (response)
        {
          this.snackBar.showInfo(
            "Saved successfully.",
            "",
            null,
            "left",
            "bottom"
          );
          this.isLoading = false;
        }
      },
      (error) =>
      {
        this.snackBar.showInfo(error.error.title, "", null, "left", "bottom");
        this.isLoading = false;
      }
    );
  }
}
