import moment from "moment";
import { FieldConfig } from "../../itinerary/models/get-itinerary.model";

export class GetItineraryFilterRequest
{
}

export class GetItineraryFilterResponse
{
  public FilterConditions: FieldConfig[];
}

export class ItineraryFilter
{
  startDate: Date = new Date();
  endDate: Date = new Date();
  typeOfBookings: (string | number)[] = [];
  deselectedTypeOfBookings: (string | number)[] = [];

  constructor(isUpcoming: boolean = true)
  {
    this.startDate.setDate(1);
    if (!isUpcoming)
    {
      this.endDate = moment(new Date()).subtract(1, 'day').toDate();
      this.startDate = moment(this.endDate).subtract(30, 'day').toDate();
    }
  }
}

export class TypeOfBookingEntity
{
  name: string;
  value: string | number;
  isSelected: boolean;
}

export class ItineraryFilterDialogData
{
  FilterConditions: FieldConfig[];
  hideDateRange: boolean;
  itineraryFilter: ItineraryFilter;
  allTypes: (string | number)[];
}

export class ItineraryFilterResult extends ItineraryFilter
{
  isInitialSubmit: boolean;
}