import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteMyProfileFieldCategoryRequest, DeleteMyProfileFieldCategoryResponse, GetMyProfileFieldCategoryRequest, GetMyProfileFieldCategoryResponse, IMyProfileFieldCategoryService, PostMyProfileFieldCategoryRequest, PostMyProfileFieldCategoryResponse, PostSetCategoriesOrderRequest, PostSetCategoriesOrderResponse } from '../@intermediate/myprofile-field-category';


@Injectable({
  providedIn: 'root'
})
export class MyProfileFieldCategoryService implements IMyProfileFieldCategoryService
{

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/MyProfileFieldCategory';
  }

  Get(request: GetMyProfileFieldCategoryRequest): Observable<GetMyProfileFieldCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMyProfileFieldCategoryResponse>(apiUrl, request);
  }

  public Post(request: PostMyProfileFieldCategoryRequest): Observable<PostMyProfileFieldCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMyProfileFieldCategoryRequest>(apiUrl, request);
  }

  public Delete(request: DeleteMyProfileFieldCategoryRequest): Observable<DeleteMyProfileFieldCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<DeleteMyProfileFieldCategoryResponse>(apiUrl, { params: request as any });
  }

  public Update(request: PostMyProfileFieldCategoryRequest): Observable<PostMyProfileFieldCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '/update';
    return this.http.post<PostMyProfileFieldCategoryResponse>(apiUrl, request);
  }

  public SetCategoriesOrder(request: PostSetCategoriesOrderRequest): Observable<PostSetCategoriesOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/SetOrder';
    return this.http.post<PostSetCategoriesOrderResponse>(apiUrl, request);
  }
}

