import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SnackBarModule } from '@tei/material';
import { StatementPaymentService } from '../../@services/statement-payment.service';
import { RemoveCommaPipe, StatementPaymentComponent } from './statement-payment.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatSlideToggleModule, MatFormFieldModule, MatInputModule, MatButtonModule, MatIconModule, MatRippleModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
  declarations: [StatementPaymentComponent, RemoveCommaPipe],
  imports: [
    RouterModule.forChild([{ path: '', component: StatementPaymentComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IStatementPaymentService', useClass: StatementPaymentService }],
  exports: []
})
export class StatementPaymentModule { }
