import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { AccountSecurityComponent } from './pages/account-security/account-security.component';
import { AddressComponent } from './pages/address/address.component';
import { AddressesComponent } from './pages/addresses/addresses.component';
import { DefaultComponent } from './pages/default/default.component';
import { ItinerarySettingComponent } from './pages/itinerary-setting/itinerary-setting.component';
import { ItineraryComponent } from './pages/itinerary/itinerary.component';
import { MyProfileFieldInfoComponent } from './pages/my-profile-field-info/my-profile-field-info.component';
import { MyStatementsSettingsComponent } from './pages/my-statements-settings/my-statements-settings.component';
import { MyprofileFieldCategoryComponent } from './pages/myprofile-field-category/myprofile-field-category.component';
import { MyprofileFieldComponent } from './pages/myprofile-field/myprofile-field.component';
import { PagesComponent } from './pages/pages.component';
import { PaymentMethodComponent } from './pages/paymentmethod/paymentmethod.component';
import { PaymentMethodsComponent } from './pages/paymentmethods/paymentmethods.component';
import { RegistrationsComponent } from './pages/registrations/registrations.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { PropertyBalanceSettingsComponent } from './pages/statement-property-settings/statement-property-settings.component';
import { TransactionSettingsComponent } from './pages/transaction-settings/transaction-settings.component';
import { TransactionsComponent } from './pages/transactions/transactions.component';

const routes: Routes = [{
  path: '',
  component: PagesComponent,
  children: [
    //Empty Pages
    {
      path: '',
      component: DefaultComponent,
      data: { RouteData: new RouteData({ Title: 'My Profile' }) }
    },
    {
      path: 'settings',
      component: SettingsComponent,
      data: { RouteData: new RouteData({ Title: 'Settings' }) }
    },

    //Addresess
    {
      path: 'addresses', // add/edit address routes added to AddressesModule
      component: AddressesComponent,
      data: { RouteData: new RouteData({ Title: 'My Account' }) },
    },
    {
      path: 'address',
      redirectTo: 'address/'
    },
    {
      path: 'address',
      component: AddressComponent,
      data: { RouteData: new RouteData({ Title: 'Address Information', CanGoBack: true, BackRoute: 'addresses' }) },
    },
    {
      path: 'address/:id',
      component: AddressComponent,
      data: { RouteData: new RouteData({ Title: 'Address Information', CanGoBack: true, BackRoute: '/addresses' }) }
    },

    //Payment Methods 
    {
      path: 'paymentmethods', // add/edit payment-method routes added to PaymentMethodsModule
      component: PaymentMethodsComponent,
      data: { RouteData: new RouteData({ Title: 'Payment Methods' }) },
    },
    {
      path: 'paymentmethod',
      redirectTo: 'paymentmethod/'
    },
    {
      path: 'paymentmethod/:id',
      component: PaymentMethodComponent,
      data: { RouteData: new RouteData({ Title: 'Payment Information', CanGoBack: true, BackRoute: '/paymentmethods' }) }
    },

    //Transations
    {
      path: 'transactions',
      component: TransactionsComponent,
      data: { RouteData: new RouteData({ Title: 'My Account' }) }
    },
    //Transaction Settings
    {
      path: 'my-transactions-settings',
      component: TransactionSettingsComponent,
      data: { RouteData: new RouteData({ Title: 'My Transactions Settings' }) }
    },

    //Itinerary
    {
      path: 'itinerary',
      component: ItineraryComponent,
      data: { RouteData: new RouteData({ Title: 'My Account' }) },
    },
    //Itinerary Settings
    {
      path: 'itinerary-settings',
      component: ItinerarySettingComponent,
      data: { RouteData: new RouteData({ Title: 'Itinerary Settings' }) }
    },

    //Statements
    {
      path: 'statements',
      loadChildren: () => import('./pages/statements/statements.module').then(m => m.StatementsModule),
      data: { RouteData: new RouteData({ Title: 'My Statements' }) }
    },
    {
      path: 'statement-payment', // this route goes inside StatementsModule
      redirectTo: "statements/payment"
    },
    //Statements Settings
    {
      path: 'my-statements-settings',
      component: MyStatementsSettingsComponent,
      data: { RouteData: new RouteData({ Title: 'My Statements Settings' }) }
    },

    //My Profile
    {
      path: 'my-profile-field',
      component: MyprofileFieldComponent,
      data: { RouteData: new RouteData({ Title: 'Myprofile-Field' }) }
    },
    //My Profile Settings
    {
      path: 'my-profile-field-category',
      component: MyprofileFieldCategoryComponent,
      data: { RouteData: new RouteData({ Title: 'Myprofile-Field Category' }) }
    },
    {
      path: 'my-profile',
      component: MyProfileFieldInfoComponent,
      data: { RouteData: new RouteData({ Title: 'My Profile' }) }
    },



    {
      path: 'property-balance-settings',
      component: PropertyBalanceSettingsComponent,
      data: { RouteData: new RouteData({ Title: 'Property Balance Settings' }) }
    },

    {
      path: 'registrations',
      component: RegistrationsComponent,
      data: { RouteData: new RouteData({ Title: 'Registrations' }) }
    },

    {
      path: 'account-security',
      component: AccountSecurityComponent,
      data: { RouteData: new RouteData({ Title: 'Account Security' }) }
    },

  ]
},
{ path: '**', redirectTo: '' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MyAccountRoutingModule
{
}
