import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyProgressBarModule as MatProgressBarModule } from '@angular/material/legacy-progress-bar';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { RegistrationsService } from '../../@services/registrations.service';
import { RegistrationsComponent } from './registrations.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [
  MatTabsModule,
  MatCardModule,
  MatProgressBarModule,
  MatExpansionModule,
  MatButtonModule,
  MatChipsModule
];
const TEI_MATERIAL_MODULES = [ConfirmDialogModule, SnackBarModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [RegistrationsComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: RegistrationsComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IRegistrationsService', useClass: RegistrationsService }],
})
export class RegistrationsModule { }
