import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { FieldGroupLookupModule, FieldGroupLookupWrapperModule } from '@tei/common-field-management-shared';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { MyProfileFieldInfoService } from '../../@services/my-profile-field-info.service';
import { AddressContainerComponent } from '../../components/address-container/address-container.component';
import { MyProfileFieldInfoComponent } from './my-profile-field-info.component';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule, RouterModule];
const MATERIAL_MODULES = [MatExpansionModule, MatRippleModule, MatFormFieldModule, MatInputModule, MatSelectModule, MatSlideToggleModule, MatRadioModule, MatButtonModule, MatCheckboxModule];
const TEI_MATERIAL_MODULES = [FieldGroupLookupModule, FieldGroupLookupWrapperModule, BreadCrumbModule, LoadingModule];
@NgModule({
  declarations: [MyProfileFieldInfoComponent, AddressContainerComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: MyProfileFieldInfoComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IMyProfileFieldInfoService', useClass: MyProfileFieldInfoService }],
})
export class MyProfileFieldInfoModule { }
