import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetManageAddressResponse } from './get-manage-address.model';
import { Address } from './address.model';

export class PostManageAddressRequest
{
  public AddressID: string;
  public FirstName: string;
  public LastName: string;
  public AddressLine: string;
  public City: string;
  public CountryCode: string;
  public RegionCode: string;
  public CountryName: string;
  public RegionName: string;
  public PostalCode: string;
  public isDisplayed: string;
}

export class PostManageAddressRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      AddressID: new UntypedFormControl(''),
      FirstName: new UntypedFormControl(''),
      LastName: new UntypedFormControl(''),
      AddressLine: new UntypedFormControl(''),
      City: new UntypedFormControl(''),
      CountryCode: new UntypedFormControl(''),
      RegionCode: new UntypedFormControl(''),
      CountryName: new UntypedFormControl(''),
      RegionName: new UntypedFormControl(''),
      PostalCode: new UntypedFormControl(''),
      isDisplayed: new UntypedFormControl('')
    });
  }

  public value: PostManageAddressRequest;

  public setValue(value: GetManageAddressResponse)
  {
    const request = new PostManageAddressRequest();
    if (value.Address)
    {
      request.AddressID = value.Address.AddressID;
      request.FirstName = value.Address.FirstName;
      request.LastName = value.Address.LastName;
      request.AddressLine = value.Address.AddressLine;
      request.City = value.Address.City;
      request.CountryCode = value.Address.CountryCode;
      request.RegionCode = value.Address.RegionCode;
      request.CountryName = value.Address.CountryName;
      request.RegionName = value.Address.RegionName;
      request.PostalCode = value.Address.PostalCode;
      request.isDisplayed = value.Address.isDisplayed;
      super.setValue(request);
    }
  }
}

export class PostManageAddressResponse
{
  public Address: Address;
}
