import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSettingsRequest, GetSettingsResponse, ISettingsService, PostSettingsRequest, PostSettingsResponse } from '../@intermediate/settings';

@Injectable({
  providedIn: 'root'
})

export class SettingsService implements ISettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    // this.serviceUrl = this.context._properties.routingWebAPI + "Settings";
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/Settings';
  }

  public Get(request: GetSettingsRequest): Observable<GetSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetSettingsResponse>(apiUrl, { params: {} });
  }

  public Post(request: PostSettingsRequest): Observable<PostSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.post<PostSettingsResponse>(apiUrl, request);
  }
}
