import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { TranslateService } from '@ngx-translate/core';
import { AbstractFormGroupControlValueAccessor } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { GetMyStatementsSettingsRequest, GetMyStatementsSettingsResponse, IMyStatementsSettingsService, PostMyStatementsSettingsRequest, PostMyStatementsSettingsRequestForm, PostMyStatementsSettingsResponse } from '../../@intermediate/my-statements-settings';
import { StatementPaymentSetting } from '../../@intermediate/statement-payment-setting';
import { StatementTableSetting } from '../../@intermediate/statements-table-settings/models/post-statement-table-setting.model';

@Component({
  selector: "tei-my-account-pages-my-statements-settings",
  templateUrl: "./my-statements-settings.component.html",
  styleUrls: ["./my-statements-settings.component.scss"],

})
export class MyStatementsSettingsComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetMyStatementsSettingsResponse;
  form: PostMyStatementsSettingsRequestForm;
  breadCrumbData: BreadCrumb[];
  isLoadingResults = false;
  statementsSettingsResponse: PostMyStatementsSettingsResponse;
  openInvoice: string;
  displayZeroOrFullyPaidInvoices: boolean | "";

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject("IMyStatementsSettingsService")
    private myStatementsSettingsService: IMyStatementsSettingsService,
    private translate: TranslateService,
    private snackBar: SnackBarService
  )
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostMyStatementsSettingsRequestForm();
    const request = new GetMyStatementsSettingsRequest();
    this.load(request);
    this.setBreadCrumbData();
  }

  setBreadCrumbData()
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
        relativeUrl: '/office/contacts/setup'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
        relativeUrl: '/office/contacts/setup/web-account-configuration'
      },
      {
        title: this.translate.instant('MY_ACCOUNT.STATEMENT_SETTINGS.MY_STATEMENTS'),
        relativeUrl: "",
      },
    ];
  }

  private load(request: GetMyStatementsSettingsRequest)
  {
    if (!this.model)
    {
      this.model = new GetMyStatementsSettingsResponse();
    }
    this.turnOnProgressBar();
    this.myStatementsSettingsService.Get(request).subscribe(
      (response) =>
      {
        this.turnOffProgressBar();
        this.model = response;
        if (this.model && response?.Settings?.length > 0)
        {
          const paymentSetting = this.model.Settings?.map(f => f.StatementPaymentSettings).find(f => f.ApplyPaymentsTo);
          this.openInvoice = paymentSetting.ApplyPaymentsTo;
          this.displayZeroOrFullyPaidInvoices = paymentSetting.DisplayZeroOrFullyPaidInvoices;
          this.form.setValue(this.model);
        }
      },
      (error) =>
      {
        this.snackBar.showInfo(error.error.title, "", null, "center", "bottom");
        this.turnOffProgressBar();
      }
    );
  }

  save(form: PostMyStatementsSettingsRequestForm)
  {
    this.turnOnProgressBar();
    let request = new PostMyStatementsSettingsRequest();
    request.BalanceType = form.get("BalanceType").value;
    request.DisplayType = form.get("DisplayType").value;
    request.ShowAccBalanceTo = form.get("ShowAccBalanceTo").value;
    request.StatementPaymentSettings = form.get("StatementPaymentSettings").value as StatementPaymentSetting;
    request.StatementTableSettings = form.get("StatementTableSettings").value as StatementTableSetting;
    this.validatePaymentSettingsDefaultValue(request);
    this.validateTableSettingDefaultValue(request);

    this.myStatementsSettingsService.Post(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.statementsSettingsResponse = response;
      if (this.statementsSettingsResponse.IsSuccess)
      {
        this.snackBar.showInfo('Settings saved.', '', null, 'left', 'bottom');
      }
    },
      error =>
      {
        this.snackBar.showInfo(error.error.title, '', null, 'left', 'bottom');
        this.turnOffProgressBar();
      });
  }

  private validatePaymentSettingsDefaultValue(request: PostMyStatementsSettingsRequest)
  {
    if (request.StatementPaymentSettings != "")
    {
      request.StatementPaymentSettings.EnableApplyPaymentByDefault = request.StatementPaymentSettings.EnableApplyPaymentByDefault == "" ? false : request.StatementPaymentSettings.EnableApplyPaymentByDefault;
      request.StatementPaymentSettings.DisplayZeroOrFullyPaidInvoices = request.StatementPaymentSettings.DisplayZeroOrFullyPaidInvoices == "" ? false : request.StatementPaymentSettings.DisplayZeroOrFullyPaidInvoices;
      request.StatementPaymentSettings.EnableCreditCardPayments = request.StatementPaymentSettings.EnableCreditCardPayments == "" ? false : request.StatementPaymentSettings.EnableCreditCardPayments;
      request.StatementPaymentSettings.EnableStatementPayments = request.StatementPaymentSettings.EnableStatementPayments == "" ? false : request.StatementPaymentSettings.EnableStatementPayments;
      request.StatementPaymentSettings.EnableECheckOrACHPayments = request.StatementPaymentSettings.EnableECheckOrACHPayments == "" ? false : request.StatementPaymentSettings.EnableECheckOrACHPayments;
      request.StatementPaymentSettings.EnablePreAuthorizedPayments = request.StatementPaymentSettings.EnablePreAuthorizedPayments == "" ? false : request.StatementPaymentSettings.EnablePreAuthorizedPayments;
    }

  }

  private validateTableSettingDefaultValue(request: PostMyStatementsSettingsRequest)
  {
    if (request.StatementTableSettings != "")
    {
      request.StatementTableSettings.DisplayPendingFilter = request.StatementTableSettings.DisplayPendingFilter == "" ? false : request.StatementTableSettings.DisplayPendingFilter;
      request.StatementTableSettings.EnablePendingFilterByDef = request.StatementTableSettings.EnablePendingFilterByDef == "" ? false : request.StatementTableSettings.EnablePendingFilterByDef;
      request.StatementTableSettings.EnableAddress = request.StatementTableSettings.EnableAddress == "" ? false : request.StatementTableSettings.EnableAddress;
      request.StatementTableSettings.EnableInvoiceID = request.StatementTableSettings.EnableInvoiceID == "" ? false : request.StatementTableSettings.EnableInvoiceID;
      request.StatementTableSettings.EnableMemberName = request.StatementTableSettings.EnableMemberName == "" ? false : request.StatementTableSettings.EnableMemberName;
      request.StatementTableSettings.EnableType = request.StatementTableSettings.EnableType == "" ? false : request.StatementTableSettings.EnableType;
      request.StatementTableSettings.EnableViewStatement = request.StatementTableSettings.EnableViewStatement == "" ? false : request.StatementTableSettings.EnableViewStatement;
      request.StatementTableSettings.GeneralPagination = request.StatementTableSettings.GeneralPagination == null ? 25 : request.StatementTableSettings.GeneralPagination;
    }

  }

  turnOffProgressBar()
  {
    this.isLoadingResults = false;
  }

  turnOnProgressBar()
  {
    this.isLoadingResults = true;
  }

}
