import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMyStatementsSettingsRequest, GetMyStatementsSettingsResponse, IMyStatementsSettingsService, PostMyStatementsSettingsRequest, PostMyStatementsSettingsResponse } from '../@intermediate/my-statements-settings';
import { GetStatementPropertySettingsRequest, GetStatementPropertySettingsResponse, PostStatementPropertySettingsRequest, PostStatementPropertySettingsResponse } from '../@intermediate/statement-property-settings';

@Injectable({
  providedIn: 'root'
})

export class MyStatementsSettingsService implements IMyStatementsSettingsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/MyAccount/StatementSettings';
  }

  public Get(request: GetMyStatementsSettingsRequest): Observable<GetMyStatementsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetStatementSettings';
    return this.http.get<GetMyStatementsSettingsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostMyStatementsSettingsRequest): Observable<PostMyStatementsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/PostStatementSettings';
    return this.http.post<PostMyStatementsSettingsResponse>(apiUrl, request);
  }

  GetPropertySettings(request: GetStatementPropertySettingsRequest): Observable<GetStatementPropertySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetPropertyBalanceSettings';
    return this.http.get<GetStatementPropertySettingsResponse>(apiUrl, { params: request as any });
  }
  PostPropertySettings(request: PostStatementPropertySettingsRequest): Observable<PostStatementPropertySettingsResponse>
  {
     const apiUrl = this.serviceUrl + '/PostPropertyBalanceSettings';
    return this.http.post<PostStatementPropertySettingsResponse>(apiUrl, request);
  }
}
