import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-my-account-pin-barcode',
  templateUrl: './pin-barcode.component.html',
  styleUrls: ['./pin-barcode.component.css']
})
export class PinBarcodeComponent implements OnInit
{
  encryptedPin: string;
  name: string;

  constructor(public dialogRef: MatDialogRef<PinBarcodeComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private teiTextTranslateService: TranslateService)
  {
    this.encryptedPin = data.EncyptedPIN;
    this.name = data.Name;
  }

  ngOnInit(): void
  {

  }

}
