import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySnackBar as MatSnackBar } from '@angular/material/legacy-snack-bar';
import { MatLegacyTable as MatTable } from '@angular/material/legacy-table';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { GetMyProfileFieldRequest, GetMyProfileFieldResponse, IMyProfileFieldService, MasterFieldReorder, RoleSettingEnum, SortOrderMyProfileFieldRequest } from '../../@intermediate/myprofile-field';
import { MasterFieldPermission, PostMyProfileFieldRequest } from '../../@intermediate/myprofile-field/models/post-myprofile-field.model';
import { FieldChooserComponent } from '../../components/field-chooser/field-chooser.component';

export interface PeriodicElement
{
  name: string;
  position: number;
  weight: number;
  symbol: string;
}

export interface GroupBy
{
  initial: string;
  isGroupBy: boolean;
}

@Component({
  selector: 'tei-my-account-myprofile-field',
  templateUrl: './myprofile-field.component.html',
  styleUrls: ['./myprofile-field.component.css']
})
export class MyprofileFieldComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
      relativeUrl: '/office/contacts/setup'
    },
    {
      title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
      relativeUrl: '/office/contacts/setup/web-account-configuration'
    },
    {
      title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.PROFILE'),
      relativeUrl: ''
    }
  ];

  displayedColumns: string[] = [];
  dataSource;
  @ViewChild('table') table: MatTable<any>;

  form: UntypedFormGroup;

  dragDisabled = true;

  model: GetMyProfileFieldResponse;
  RoleSettingEnum = RoleSettingEnum;

  constructor(
    @Inject('IMyProfileFieldService')
    private myProfileFieldService: IMyProfileFieldService,
    private router: Router,
    public dialog: MatDialog,
    private fb: UntypedFormBuilder,
    private snackBar: MatSnackBar,
    private translate: TranslateService
  )
  {
    this.form = this.fb.group({
      MasterFieldPermissions: this.fb.array([])
    });
  }

  ngOnInit(): void
  {
    this.load();
  }

  FillDataSource(model: GetMyProfileFieldResponse)
  {
    let dataSource: any[] = [];

    model.FieldGroups.forEach(group =>
    {
      dataSource.push({ FieldGroupID: group.FieldGroupID, GroupName: group.Name, IsGroupBy: true });
      model.MyProfileFields.filter(x => x.FieldGroupName == group.Name).forEach(profileField =>
      {
        dataSource.push(profileField);
      });
    });

    this.dataSource = dataSource;
  }

  isGroup(index, item): boolean
  {
    return item.IsGroupBy;
  }

  getRoleValue(title: string, roleIdentifier: string)
  {
    const roles = this.model.MyProfileFields.find(x => x.Title == title).Roles;

    return roles.find(x => x.RoleIdentifier == roleIdentifier).Value;
  }

  getRoleStatus(title: string, roleIdentifier: string)
  {
    const roles = this.model.MyProfileFields.find(x => x.Title == title).Roles;

    const status = roles.find(x => x.RoleIdentifier == roleIdentifier).Status;
    return status == 1 ? false : true;
    // return false;
  }

  goToCategory()
  {
    this.router.navigate(['/MyprofileFieldCategory']);
  }

  openfieldChooserDialog()
  {
    const dialogRef = this.dialog.open(FieldChooserComponent, {
      height: '500px',
      width: '400px',
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.load();
      this.load();
    });
  }

  load()
  {
    this.myProfileFieldService.Get(new GetMyProfileFieldRequest()).subscribe(response =>
    {
      this.displayedColumns = [];
      this.displayedColumns.push('Title');
      response.TEIRoles.forEach(x =>
      {
        this.displayedColumns.push(x);
      });

      this.model = response;
      this.FillDataSource(this.model);
    },
      error => { this.openSnackBar("Failed", ""); });

  }

  dropTable(event: CdkDragDrop<any[]>)
  {
    const prevIndex = this.dataSource.findIndex((d) => d === event.item.data);

    if (this.dataSource[prevIndex].IsGroupBy || event.currentIndex == 0)
    {
      return;
    }

    moveItemInArray(this.dataSource, prevIndex, event.currentIndex);
    this.table.renderRows();
  }

  save()
  {
    var sortOrderRequest = new SortOrderMyProfileFieldRequest();
    this.saveReordering(sortOrderRequest);

    var permissionRequest = new PostMyProfileFieldRequest();

    this.model.MyProfileFields.forEach(field =>
    {
      field.Roles.forEach(role =>
      {
        let masterFieldPermission = new MasterFieldPermission();
        masterFieldPermission.MasterFieldID = field.MasterFieldID;
        masterFieldPermission.RoleIdentifier = role.RoleIdentifier;
        masterFieldPermission.Value = role.Value;
        masterFieldPermission.MasterFieldPermissionID = role.MasterFieldPermissionID;
        masterFieldPermission.Status = role.Status;
        permissionRequest.MasterFieldPermissions.push(masterFieldPermission);
      });
    });
    this.myProfileFieldService.Post(permissionRequest).subscribe(response =>
    {
      this.openSnackBar("Saved", "");
    },
      error => { this.openSnackBar(error, ""); });
  }

  saveReordering(request: SortOrderMyProfileFieldRequest)
  {
    let fieldGroupID: number;
    let orderNo: number = 1;

    this.dataSource.forEach(row =>
    {
      if (row?.IsGroupBy)
      {
        fieldGroupID = row.FieldGroupID;
        orderNo = 1;
      } else
      {
        let masterField = new MasterFieldReorder();
        masterField.FieldGroupID = fieldGroupID;
        masterField.OrderNo = orderNo;
        masterField.FieldGroupMasterFieldID = row.FieldGroupMasterFieldID;

        orderNo++;
        request.MasterFieldReorders.push(masterField);
      }
    });

    this.myProfileFieldService.SortOrder(request).subscribe(response =>
    {
    });
  }

  get getFields(): UntypedFormArray
  {
    return this.form.controls["MasterFieldPermissions"] as UntypedFormArray;
  }

  newField(): UntypedFormGroup
  {
    return this.fb.group({
      MasterFieldID: 0,
      RoleIdentifier: "",
      Value: "",
      SortOrder: 0,
      Status: 0
    });
  }

  addField()
  {
    this.getFields.push(this.newField());
  }

  onRoleValueChange(field, event, roleIdentifier)
  {
    let roles = this.model.MyProfileFields.find(x => x.Title == field.Title).Roles;
    roles.find(x => x.RoleIdentifier == roleIdentifier).Value = event.value;
  }

  openSnackBar(message: string, action: string)
  {
    this.snackBar.open(message, action, {
      duration: 3 * 1000,
    });
  }
}



