import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
  name: 'peopleCount'
})
export class PeopleCountPipe implements PipeTransform {

  constructor(private teiTextTranslateService: TranslateService) { }
  transform(value: number): string {
    if (value === 1)
    {
      return this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.JUST_ONE_PERSON');
    }
    else
    {
      return value + ' ' +
        this.teiTextTranslateService.instant('MY_ACCOUNT.DINING.PEOPLE');
    }
  }
}
